@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Roboto";
  font-display: swap;
  src: url("../fonts/roboto-regular.woff2") format("woff2"), url("../fonts/roboto-regular.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 500;
  font-family: "Roboto";
  font-display: swap;
  src: url("../fonts/roboto-medium.woff2") format("woff2"), url("../fonts/roboto-medium.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Roboto";
  font-display: swap;
  src: url("../fonts/roboto-bold.woff2") format("woff2"), url("../fonts/roboto-bold.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 19px;
  font-family: "Roboto", "Arial", sans-serif;
  color: #333333;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #f2f2f2;
}

a {
  color: #333333;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.rouble {
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.btn-reset {
  padding: 0;
  border: none;
  background-color: rgba(255, 255, 255, 0);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.container {
  width: 100%;
  max-width: 1440px;
  margin: 0 auto;
  padding: 0 22px;
}

.header {
  padding: 22px 0;
  background-color: rgba(255, 255, 255, 0);
}
.header .container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header__logo {
  display: block;
  width: 236px;
  height: 70px;
}
.header__logo svg {
  width: 187px;
  height: 70px;
}

.main-nav {
  display: flex;
  align-items: center;
  justify-content: center;
}
.main-nav__list {
  display: flex;
  flex-wrap: wrap;
  column-gap: 60px;
  row-gap: 20px;
  margin: 0 40px;
  padding: 11px 46px;
  border: 2px solid #ffffff;
  border-radius: 20px;
  list-style: none;
}
@media (max-width: 1279px) {
  .main-nav__list {
    column-gap: 30px;
    min-width: 362px;
  }
}
.main-nav__item {
  position: relative;
}
.main-nav__item--notifications.is-notifications .main-nav__link::before {
  content: "";
  position: absolute;
  top: 2px;
  right: 4px;
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background-color: #c5ec2a;
}
.main-nav__item--notifications:hover .main-nav__dropdown, .main-nav__item--notifications:focus-within .main-nav__dropdown {
  display: block;
}
.main-nav__link {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 44px;
  height: 44px;
  border: 1px solid rgba(255, 255, 255, 0);
  border-radius: 50%;
  color: #aeaeae;
  transition: background-color 0.3s ease, color 0.3s ease;
  outline: none;
}
.main-nav__link.is-active {
  background-color: #333333;
  color: #c5ec2a;
  pointer-events: none;
}
.main-nav__link.is-disabled {
  color: #333333;
  opacity: 0.15;
  user-select: none;
}
@media (hover: hover) {
  .main-nav__link:hover:not(.focus-visible) {
    color: #c5ec2a;
  }
}
.main-nav__link.focus-visible:focus {
  border: 1px solid #181818;
}
.main-nav__dropdown {
  z-index: 10;
  position: absolute;
  display: none;
  top: calc(100% + 35px);
  left: -25px;
  width: 374px;
  padding: 30px 10px 15px;
  border-radius: 16px;
  background-color: #ffffff;
  box-shadow: 10px 10px 150px rgba(51, 51, 51, 0.25);
}
.main-nav__dropdown::before {
  content: "";
  position: absolute;
  top: -8px;
  left: 40px;
  width: 16px;
  height: 16px;
  border-radius: 0 0 0 2px;
  background-color: #ffffff;
  clip-path: polygon(0% 0%, 100% 100%, 0% 100%);
  transform: rotate(135deg);
}
.main-nav__dropdown::after {
  content: "";
  position: absolute;
  top: -35px;
  left: 0;
  width: 100%;
  height: 35px;
}
.main-nav__label {
  margin: 0 10px 10px;
  font-weight: 500;
  font-size: 16px;
  line-height: 16px;
  letter-spacing: 0.01em;
  text-transform: uppercase;
  color: #181818;
}
.main-nav__sublist {
  margin: 0;
  padding: 0;
  list-style: none;
}
.main-nav__subitem {
  width: 100%;
}

.footer {
  padding: 40px 0;
  background-color: rgba(255, 255, 255, 0);
}

.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  background-color: rgba(0, 0, 0, 0.8);
  transform: scale(1.2);
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.6s cubic-bezier(0.55, 0, 0.1, 1), transform 0.6s cubic-bezier(0.55, 0, 0.1, 1), visibility 0.6s cubic-bezier(0.55, 0, 0.1, 1);
  pointer-events: none;
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.modal::-webkit-scrollbar {
  display: none;
}
.modal__wrapper {
  position: relative;
  display: flex;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 100%;
  padding: 60px 40px;
}
@media (max-width: 767px) {
  .modal__wrapper {
    padding: 60px 16px;
  }
}
.modal__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
}
.modal__content {
  position: relative;
  width: 600px;
  padding: 40px;
  background-color: #ffffff;
}
@media (max-width: 767px) {
  .modal__content {
    width: 100%;
    padding: 20px;
  }
}
.modal__close-btn {
  position: absolute;
  top: 22px;
  right: 22px;
  z-index: 1;
  width: 40px;
  height: 40px;
  padding: 0;
  background: rgba(255, 255, 255, 0);
  border: none;
  cursor: pointer;
}
.modal--responsive .modal__content {
  width: 100%;
}
.modal--fit-content .modal__content {
  width: auto;
}
@media (max-width: 767px) {
  .modal--fit-content .modal__content {
    width: 100%;
  }
}
.modal--no-scale {
  transform: none;
}
.modal--preload {
  transition: none;
}
.modal.is-active {
  transform: scale(1);
  visibility: visible;
  opacity: 1;
  pointer-events: auto;
}

.btn {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  row-gap: 20px;
  padding: 25px 20px;
  width: 100%;
  color: #181818;
  font-size: 16px;
  font-weight: 500;
  text-transform: uppercase;
  line-height: 16px;
  background-color: #c5ec2a;
  border: 2px solid transparent;
  border-radius: 80px;
  cursor: pointer;
  transition: color 0.3s ease, background-color 0.3s ease, border 0.3s ease, box-shadow 0.3s ease;
}
.btn--medium {
  font-size: 12px;
  padding: 18px 15px;
}
.btn--small {
  font-size: 12px;
  padding: 12px 10px;
}
.btn:disabled, .btn.is-disabled {
  color: #a8a8a8;
  background-color: #f0f0f0;
  opacity: 1;
  pointer-events: none;
}
@media (hover: hover) {
  .btn:hover:not(.focus-visible) {
    background-color: #d5ff2d;
  }
}
.btn.focus-visible:focus {
  border: 2px solid #333333;
  outline: none;
}
@media (hover: hover) {
  .btn:active:not(.focus-visible) {
    background-color: #bbe028;
    border: 2px solid transparent;
    outline: none;
  }
}
.btn--outlined {
  background-color: transparent;
  border: 1px solid #cfcfcf;
}
@media (hover: hover) {
  .btn--outlined:hover:not(.focus-visible) {
    background-color: transparent;
    border: 1px solid #333333;
  }
}
.btn--outlined.focus-visible:focus {
  background-color: transparent;
  border: 1px solid #333333;
  box-shadow: 0 0 0 2px #c5ec2a;
  outline: none;
}
@media (hover: hover) {
  .btn--outlined:active:not(.focus-visible) {
    border: 1px solid #333333;
    background-color: #f7f7f7;
    outline: none;
  }
}
.btn--outlined:disabled, .btn--outlined.is-disabled {
  border: 1px solid transparent;
}
.btn--dark-bg:disabled, .btn--dark-bg.is-disabled {
  color: #747474;
  background-color: rgba(247, 247, 247, 0.08);
  opacity: 1;
  pointer-events: none;
}
.btn--dark-bg.focus-visible:focus {
  border: 2px solid #181818;
  outline: none;
  box-shadow: 0 0 0 2px #c5ec2a;
}
@media (hover: hover) {
  .btn--dark-bg:active:not(.focus-visible) {
    border: 2px solid transparent;
    outline: none;
    box-shadow: none;
  }
}
.btn--dark-bg.btn--outlined {
  color: #c5ec2a;
  background-color: transparent;
  border: 1px solid #c5ec2a;
}
@media (hover: hover) {
  .btn--dark-bg.btn--outlined:hover:not(.focus-visible) {
    color: #cfcfcf;
    border: 1px solid #cfcfcf;
  }
}
.btn--dark-bg.btn--outlined.focus-visible:focus {
  color: #cfcfcf;
  border: 1px solid #f2f2f2;
  box-shadow: 0 0 0 2px #c5ec2a;
}
@media (hover: hover) {
  .btn--dark-bg.btn--outlined:active:not(.focus-visible) {
    color: #181818;
    border: 1px solid #333333;
    background-color: #f7f7f7;
  }
}
.btn--dark-bg.btn--outlined:disabled, .btn--dark-bg.btn--outlined.is-disabled {
  color: #747474;
  background-color: rgba(247, 247, 247, 0.08);
  border: 1px solid transparent;
}

.btn-flat {
