  font-size: 14px;
  font-weight: 500;
  text-decoration: underline transparent;
  background-color: transparent;
  border: none;
  border-bottom: 2px solid transparent;
  cursor: pointer;
  transition: border 0.3s ease, background-color 0.3s ease, opacity 0.3s ease, text-decoration 0.3s ease, box-shadow 0.3s ease;
