@media (hover: hover) {
  .btn-flat--underlined:hover:not(.focus-visible) {
    text-decoration: underline #c5ec2a;
    /* stylelint-disable-next-line */
    -webkit-text-decoration: underline #c5ec2a;
    border-bottom: 2px solid transparent;
  }
