.thumbnail-user {
  position: relative;

  display: grid;
  grid-template-columns: 82px 1fr;
  grid-template-rows: min-content min-content auto;
  gap: 14px 16px;
  align-items: start;

  min-width: 334px;
  width: min-content;
  height: 100%;
  padding: 30px 20px 23px;

  border: 1px solid $color-pear;
  border-radius: 20px;
  transition: box-shadow $trans-600-ease-in-out;

  &--role-user {
    box-shadow: 0 10px 30px rgba($color: $color-night-rider, $alpha: 0.1);
    background-color: $color-default-white;
    color: $color-night-rider;
  }

  &--role-coach {
    box-shadow: 0 10px 30px rgba($color: $color-night-rider, $alpha: 0.33);
    background-color: $color-night-rider;
    color: $color-default-white;
  }

  &--dark {
    border: 1px solid rgba($color-nobel-2, 0.2);
    border-radius: 8px;
    background-color: $color-eclipse;
    color: $color-default-white;

    .thumbnail-user__hashtag {
      border: 1px solid $color-dark-gray-3;
    }

    .thumbnail-user__button {
      margin-top: 16px;
    }
  }

  @include hover {
    box-shadow: none;
  }
}

.thumbnail-user__image {
  grid-column: 1 / 2;
  grid-row: 1 / 3;

  width: 82px;
  height: 82px;

  border-radius: 50%;
  overflow: hidden;
}

.thumbnail-user__image img {
  max-width: 100%;
  height: auto;
  object-fit: cover;
}

.thumbnail-user__top-status {
  position: absolute;
  left: 15px;
  top: 20px;
  width: 32px;
  height: 32px;

  border-radius: 50%;

  &--role-user {
    background-color: $color-night-rider;
    color: $color-pear;
  }

  &--role-coach {
    background-color: $color-pear;
    color: $color-night-rider;
  }
}

.thumbnail-user__top-status svg {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate3d(-50%, -50%, 0);

  width: 12px;
  height: 12px;
}

.thumbnail-user__header {
  grid-column: 2 / 3;
  grid-row: 1 / 2;

  display: flex;
  flex-direction: column;
}

.thumbnail-user__name {
  margin: 0;
  margin-bottom: 4px;

  font-size: 22px;
  font-weight: 500;
  line-height: 1;
  letter-spacing: 0.01em;
  text-transform: uppercase;
}

.thumbnail-user__location {
  display: flex;
  align-items: center;

  font-size: 12px;
  line-height: 1;
  color: $color-dark-grey;
}

.thumbnail-user__location svg {
  flex: 0 0 14px;
  width: 14px;
  height: 16px;
  align-self: flex-start;
}

.thumbnail-user__location-address {
  margin-left: 6px;

  font-style: normal;
}

.thumbnail-user__hashtags-list {
  grid-column: 2 / 3;
  grid-row: 2 / 3;

  margin: 0;
  padding: 0;
  list-style-type: none;

  display: flex;
  flex-wrap: wrap;
}

.thumbnail-user__hashtags-item {
  margin-right: 8px;
  margin-bottom: 8px;
}

.thumbnail-user__hashtag {
  padding: 6px 10px;

  font-size: 12px;
  line-height: 1;

  color: inherit;
  border-color: $color-very-light-grey;
}

.thumbnail-user__button {
  grid-column: 1 / 3;
  grid-row: 3 / 4;
  align-self: end;

  margin-top: 8px;
}
