.custom-select {
  position: relative;
  display: flex;
  flex-direction: column;

  width: 100%;

  @media screen and (max-width: 767px) {
    padding: 4px 0 14px;
    width: 100%;
  }

  &__label {
    color: $color-dark-gray-2;
    margin-bottom: 9px;
    pointer-events: none;

    .is-disabled & {
      color: rgba($color-dark-gray-2, 0.5);
    }
  }

  &__placeholder {
    position: absolute;
    bottom: 17px;
    left: 20px;
    color: $color-night-rider;
    pointer-events: none;

    .is-disabled & {
      color: rgba($color-night-rider, 0.5);
    }

    @media screen and (max-width: 767px) {
      transform: translateY(calc(-50% - 5px));
    }
  }

  select {
    position: absolute;

    width: 1px;
    height: 1px;
    margin: -1px;
    padding: 0;
    overflow: hidden;

    white-space: nowrap;

    border: 0;

    clip: rect(0 0 0 0);
    clip-path: inset(100%);
  }

  &__button {
    display: flex;
    align-items: center;
    width: 100%;
    height: 50px;
    padding: 14px 20px;

    background-color: rgba($color-dark-gray-2, 0.1);
    border: 1px solid rgba($color-nero, 0.1);
    border-radius: 8px;
    outline: none;
    cursor: pointer;

    transition: border $trans-default, box-shadow $trans-default, color $trans-default, background-color $trans-default;

    &:hover,
    &:active {
      outline: none;
      background-color: transparent;
      border: 1px solid $color-night-rider;
      box-shadow: none;
    }

    &:focus {
      outline: none;
      background-color: transparent;
      border: 1px solid $color-night-rider;
      box-shadow: 0 0 0 2px $color-pear;
    }

    .custom-select--not-selected & {
      background-color: transparent;
      border: 1px solid $color-very-light-grey;
    }

    &:disabled,
    .is-disabled & {
      border: 1px solid rgba($color-nero, 0.08);
      background-color: rgba($color-dark-gray-2, 0.08);
      // opacity: 0.08;
    }

    .custom-select--readonly &:disabled {
      border: none;
      background-color: transparent;
      pointer-events: none;
    }
  }

  &__list {
    position: absolute;
    bottom: 10px;
    z-index: 1;

    width: 100%;
    max-height: 100px;
    margin: 0;
    padding: 0;
    overflow-y: auto;

    list-style: none;

    background-color: #ffffff;
    border: 2px solid #000000;
    transform: translateY(100%);
    visibility: hidden;
    opacity: 0;

    transition: visibility 0.3s ease, opacity 0.3s ease;
  }

  &__item {
    padding: 5px;

    outline: none;
    cursor: pointer;

    transition: background-color 0.3s ease;

    &[aria-selected="true"] {
      background-color: rgba(0, 0, 0, 0.2);

      @media (hover: hover) {
        &:hover:not(.focus-visible) {
          background-color: rgba(0, 0, 0, 0.2);
        }
      }

      &.focus-visible:focus {
        background-color: rgba(0, 0, 0, 0.2);
      }
    }

    @media (hover: hover) {
      &:hover:not(.focus-visible) {
        background-color: rgba(0, 0, 0, 0.4);
      }
    }

    &.focus-visible:focus {
      background-color: rgba(0, 0, 0, 0.4);
    }
  }

  &__error {
    position: absolute;
    bottom: -15px;
    left: 0;

    color: $color-venetian-red;

    opacity: 0;

    transition: opacity 0.1s ease;
  }

  &__text {
    font-size: 20px;
    line-height: 24px;
    flex-shrink: 0;
    width: 80%;
    overflow: hidden;

    text-align: left;
    white-space: nowrap;
    text-overflow: ellipsis;

    opacity: 0;

    transition: opacity 0.3s ease 0.1s;

    br {
      display: none;
    }
  }

  &__icon {
    flex-shrink: 0;
    margin-left: auto;
    color: rgba($color-night-rider, 0.2);

    .custom-select__button:disabled ~ &,
    .is-disabled & {
      color: rgba($color-night-rider, 0.2);
    }
  }

  &--not-selected &__icon {
    color: $color-night-rider;
  }

  &--readonly &__icon {
    display: none;
  }

  &.is-disabled {
    opacity: 1;
    pointer-events: none;

    .custom-select__list {
      display: none;
    }
  }

  &.open-up {
    .custom-select__list {
      bottom: auto;
      top: 0;
      transform: translateY(calc(-100% + 10px));
    }
  }

  &.is-open {
    .custom-select__list {
      visibility: visible;
      opacity: 1;
    }

    .custom-select__icon {
      transform: rotate(180deg);
    }

    .custom-select__label {
      opacity: 0;
    }
  }

  &.not-empty {
    .custom-select__text {
      opacity: 1;
    }

    .custom-select__label {
      opacity: 0;
    }

    .custom-select__placeholder {
      opacity: 0;
    }
  }

  &.is-invalid {
    .custom-select__error {
      opacity: 1;
    }

    .custom-select__button {
      border: 2px solid $color-venetian-red;

      &:focus,
      &:hover {
        border: 2px solid rgba(#ff1553, 0.5);
      }
    }
  }

  /* &.is-valid {
    .custom-select__button {
      border: 2px solid $color-eucalyptus;

      &:focus,
      &:hover {
        border: 2px solid rgba($color-eucalyptus, 0.5);
      }
    }
  } */

  &--placeholder {
    .custom-select__label {
      transform: translateY(calc(-50% - 40px));
      opacity: 1;
    }

    &.is-open .custom-select__label {
      opacity: 1;
    }

    &.not-empty .custom-select__label {
      opacity: 1;
    }
  }

  &.slide-up.not-empty,
  &.slide-up.is-open {
    .custom-select__label {
      transform: translateY(calc(-50% - 40px));
      opacity: 1;
    }

    .custom-select__text {
      opacity: 1;
    }
  }

  .input-message {
    position: absolute;
    right: 0;
    bottom: -5px;

    &--invalid {
      color: $color-venetian-red;
    }

    &--valid {
      color: $color-pear;
    }
  }
}
