.thumbnail-training {
  display: flex;
  flex-direction: column;

  min-width: 334px;
  width: min-content;
  height: 100%;

  @include hover {
    & .thumbnail-training__inner {
      box-shadow: none;
    }

    & .thumbnail-training__image {
      filter: grayscale(0);
    }
  }
}

.thumbnail-training__inner {
  flex-grow: 1;
  position: relative;

  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 193px auto auto auto 1fr;
  align-content: start;

  padding: 20px 17px 22px 19px;
  margin-bottom: 15px;

  background-color: $color-default-white;
  color: $color-night-rider;

  border: 1px solid $color-pear;
  box-shadow: 0 10px 30px rgba($color: $color-night-rider, $alpha: 0.1);
  border-radius: 20px;
  transition: box-shadow $trans-600-ease-in-out;

  &:last-child {
    margin-bottom: 0;
  }
}

.thumbnail-training__image {
  position: absolute;
  left: 1px;
  top: 1px;
  right: 1px;

  height: 191px;

  border: 1px solid $color-default-white;
  border-radius: 20px;

  overflow: hidden;
  z-index: 1;
  filter: grayscale(1);
  transition: filter $trans-600-ease-in-out;
}

.thumbnail-training__image img {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.thumbnail-training__price {
  position: absolute;
  top: 22px;
  right: 21px;

  margin: 0;
  padding: 8px 11px;

  border-radius: 50px;

  font-size: 14px;
  line-height: 1;
  text-align: center;
  color: $color-pear;
  background-color: $color-night-rider;

  z-index: 2;
}

.thumbnail-training__price-value {
  margin-right: 4px;
}

.thumbnail-training__title {
  grid-column: 1 / 2;
  grid-row: 2 / 3;

  margin: 0;
  margin-bottom: 17px;

  font-size: 28px;
  font-weight: 500;
  line-height: 1.1;
  letter-spacing: 0.01em;
  text-transform: uppercase;
  color: $color-nero;
}

.thumbnail-training__info {
  grid-column: 1 / 2;
  grid-row: 3 / 4;

  display: flex;
  align-items: flex-start;
  margin-bottom: 3px;
}

.thumbnail-training__rate {
  display: flex;
  align-items: center;
  justify-content: end;

  height: 26px;
  margin-bottom: 8px;
}

.thumbnail-training__rate svg {
  color: $color-pear;
}

.thumbnail-training__rate-value {
  margin-left: 6px;

  font-size: 14px;
  line-height: 1;
}

.thumbnail-training__hashtags-list {
  margin: 0;
  padding: 0;
  list-style-type: none;

  display: flex;
  flex-wrap: wrap;
  margin-right: 15px;
}

.thumbnail-training__hashtags-item {
  margin-right: 7px;
  margin-bottom: 8px;
}

.thumbnail-training__hashtag {
  padding: 5px 10px;

  font-size: 12px;
  line-height: 1;

  color: inherit;
  border-color: $color-very-light-grey;
}

.thumbnail-training__text-wrapper {
  grid-column: 1 / 2;
  grid-row: 4 / 5;
}

.thumbnail-training__text {
  margin: 0;
  margin-bottom: 5px;

  font-size: 14px;
  line-height: 1.3;

  &:last-child {
    margin-bottom: 24px;
  }
}

.thumbnail-training__button-wrapper {
  grid-column: 1 / 2;
  grid-row: 5 / 6;

  flex-grow: 1;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 13px;
  align-content: end;
  margin-bottom: 7px;
}

.thumbnail-training__button-orders {
  grid-column: 1 / 2;
  grid-row: 5 / 6;
  align-self: end;
  justify-self: start;

  margin-bottom: 2px;

  line-height: 1;
}

.thumbnail-training__button-catalog {
  padding: 12px 10px;

  line-height: 1;
}

.thumbnail-training__total-info {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 14px;
}

.thumbnail-training__total-info-card {
  display: flex;
  flex-direction: column;
  padding: 11px 12px 13px;

  background-color: $color-night-rider;
  color: $color-pear;

  border-radius: 10px;
}

.thumbnail-training__total-info-card svg {
  margin-bottom: 10px;
  align-self: flex-end;
}

.thumbnail-training__total-info-value {
  margin: 0;
  margin-bottom: 7px;
  margin-top: auto;

  font-size: 20px;
  font-weight: 500;
  line-height: 1;
}

.thumbnail-training__total-info-value span {
  margin-left: 3px;
}

.thumbnail-training__total-info-text {
  margin: 0;

  font-size: 14px;
  line-height: 1;
  color: $color-very-light-grey;
}
