.thumbnail-spec-gym {
  position: relative;

  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 280px;
  row-gap: 25px;

  min-width: 334px;
  width: min-content;
  height: 100%;
  padding: 20px 20px 30px 18px;

  background-color: $color-default-white;
  color: $color-night-rider;

  border: 1px solid $color-pear;
  box-shadow: 0 10px 30px rgba($color: $color-night-rider, $alpha: 0.1);
  border-radius: 20px;
  transition: box-shadow $trans-600-ease-in-out;

  @include hover {
    box-shadow: none;

    & .thumbnail-spec-gym__image {
      filter: grayscale(0);
    }
  }
}

.thumbnail-spec-gym__image {
  position: absolute;
  left: 0;
  top: 0;
  right: 0;

  height: 300px;

  border-radius: 20px;

  overflow: hidden;
  z-index: 1;
  filter: grayscale(1);
  transition: filter $trans-600-ease-in-out;
}

.thumbnail-spec-gym__image img {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.thumbnail-spec-gym__type {
  position: absolute;
  top: 20px;
  right: 21px;

  margin: 0;
  padding: 8px 16px;

  border-radius: 50px;

  font-size: 14px;
  line-height: 1;
  text-align: center;
  color: $color-pear;
  background-color: $color-night-rider;

  z-index: 2;
}

.thumbnail-spec-gym__header {
  grid-column: 1 / 2;
  grid-row: 2 / 3;

  display: grid;
  grid-template-columns: 1fr;
  align-content: start;
  gap: 4px 15px;
}

.thumbnail-spec-gym__title {
  grid-column: 1 / 2;
  grid-row: 1 / 2;

  margin: 0;

  font-size: 28px;
  font-weight: 500;
  line-height: 1.1;
  letter-spacing: 0.01em;
  text-transform: uppercase;
  color: $color-nero;
}

.thumbnail-spec-gym__location {
  grid-column: 1 / 2;
  grid-row: 2 / 3;

  display: flex;
  align-items: center;

  font-size: 14px;
  line-height: 1;
  color: $color-dark-grey;
}

.thumbnail-spec-gym__location svg {
  flex: 0 0 14px;
  width: 14px;
  height: 16px;
  align-self: flex-start;
}

.thumbnail-spec-gym__location-address {
  margin-left: 8px;

  font-style: normal;
}

.thumbnail-spec-gym__button-wrapper {
  grid-column: 1 / 2;
  grid-row: 3 / 4;

  flex-grow: 1;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 13px;
  align-content: end;
}

.thumbnail-spec-gym__button {
  padding: 11px 10px;

  line-height: 1;
}
