.training-info {
  width: 100%;
  max-width: 1040px;
  padding: 59px;

  border: 1px solid $color-pear;
  border-bottom: 0;
  border-radius: 19px 19px 0 0;
  background-color: $color-night-rider;

  &__header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    max-width: 893px;
    margin-bottom: 30px;
  }

  &__coach {
    display: flex;
    align-items: center;
  }

  &__photo {
    width: 64px;
    height: 64px;
    margin-right: 12px;

    border-radius: 50%;

    img {
      width: 64px;
      height: 64px;

      border-radius: 50%;

      object-fit: cover;
    }
  }

  &__coach-info {
    display: flex;
    flex-direction: column;
  }

  &__label {
    display: block;
    margin-bottom: 6px;

    font-weight: 400;
    font-size: 16px;
    line-height: 16px;

    color: $color-suva-grey;
  }

  &__name {
    display: block;

    font-weight: 500;
    font-size: 18px;
    line-height: 18px;
    letter-spacing: 0.01em;
    text-transform: uppercase;

    color: $color-default-white;
  }

  &__edit {
    display: flex;
    align-items: center;

    cursor: pointer;

    &--save {
      display: none;

      text-decoration-line: underline;
    }
  }

  &__form-wrapper {
    display: grid;
    grid-template-columns: 1fr 1fr;
    column-gap: 30px;
    row-gap: 25px;
  }

  &__info-wrapper {
    grid-column: 1 / 3;

    display: grid;
    grid-template-columns: 1fr 1fr;
    column-gap: 30px;
    row-gap: 25px;
  }

  &__input {
    position: relative;
    width: 100%;

    .training-info__label {
      position: absolute;

      width: 1px;
      height: 1px;
      margin: -1px;

      clip: rect(0 0 0 0);
    }

    input {
      width: 100%;
      margin: 0;
      padding: 0;

      border: 1px solid rgba($color-nobel-2, 0.1);
      border-radius: 8px;
      background-color: rgba($color-white-smoke-3, 0.02);
      color: $color-default-white;

      outline: 0;

      transition: border $trans-default, box-shadow $trans-default;

      &:hover,
      &:active {
        border: 1px solid $color-default-white;
      }

      &:focus {
        border: 1px solid $color-default-white;
        box-shadow: 0 0 2px 2px $color-pear;
      }

      &:disabled {
        border: none;
        background-color: $color-transparent;

        pointer-events: none;
      }
    }

    &.is-invalid {
      input {
        border: 1px solid $color-radical-red;

        &:hover,
        &:focus {
          border: 1px solid $color-radical-red;
        }
      }

      .training-info__error {
        opacity: 1;
      }
    }

    &.is-disabled {
      input:disabled {
        border: 1px solid rgba($color-nobel-2, 0.1);
        background-color: rgba($color-white-smoke-3, 0.02);
      }
    }

    &--training {
      input {
        padding: 25px 19px;

        font-weight: 700;
        font-size: 34px;
        line-height: 37px;
        letter-spacing: 0.01em;
        text-transform: uppercase;
      }
    }

    &--rating {
      position: relative;
      width: auto;

      svg {
        position: absolute;
        width: 18px;
        height: 18px;
        top: 29px;
        left: 32px;

        color: $color-pear;
      }

      input {
        max-width: 99px;
        padding: 28px 31px 28px 55px;

        font-weight: 500;
        font-size: 18px;
        line-height: 18px;
        text-transform: uppercase;
      }
    }

    &--price {
      width: auto;

      .training-info__label {
        position: relative;
        display: block;

        width: auto;
        height: auto;
        margin: 0;
        margin-bottom: 10px;

        font-weight: 400;
        font-size: 16px;
        line-height: 16px;

        color: $color-suva-grey;

        clip: unset;
      }

      input {
        max-width: 213px;
        padding: 16px 19px;

        font-weight: 400;
        font-size: 16px;
        line-height: 16px;
      }
    }
  }

  &__error {
    position: absolute;
    bottom: -22px;
    left: 0;

    font-weight: 400;
    font-size: 14px;
    line-height: 14px;

    color: $color-radical-red;

    opacity: 0;
  }

  &__textarea {
    width: 100%;
    min-height: 89px;

    .training-info__label {
      position: absolute;

      width: 1px;
      height: 1px;
      margin: -1px;

      clip: rect(0 0 0 0);
    }

    textarea {
      width: 100%;
      height: 100%;
      margin: 0;
      padding: 15px 19px;

      font-weight: 400;
      font-size: 16px;
      line-height: 19px;

      border: 1px solid rgba($color-nobel-2, 0.1);
      border-radius: 8px;
      background-color: rgba($color-white-smoke-3, 0.02);
      color: $color-default-white;

      outline: 0;

      transition: border $trans-default, box-shadow $trans-default;

      &:hover,
      &:active {
        border: 1px solid $color-default-white;
      }

      &:focus {
        border: 1px solid $color-default-white;
        box-shadow: 0 0 2px 2px $color-pear;
      }

      &:disabled {
        border: none;
        background-color: $color-transparent;

        pointer-events: none;
      }
    }

    &.is-disabled {
      textarea:disabled {
        border: 1px solid rgba($color-nobel-2, 0.1);
        background-color: rgba($color-white-smoke-3, 0.02);
      }
    }
  }

  &__rating-wrapper {
    display: flex;
    align-items: flex-start;
  }

  &__list {
    display: grid;
    grid-template-columns: auto auto;
    column-gap: 12px;
    row-gap: 12px;
    margin: 0;
    margin-left: 20px;
    padding: 0;

    list-style: none;
  }

  &__item {
    width: 100%;
  }

  &__price-wrapper {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: flex-end;
    column-gap: 15px;
    row-gap: 20px;
  }

  &__buy {
    max-width: 213px;
    min-height: unset;
    padding: 18px 20px 16px;

    font-size: 14px;
    line-height: 14px;
  }


  &__discount {
    display: none;
    align-items: center;
    margin-bottom: 18px;
    margin-left: 20px;

    cursor: pointer;
  }
}
