.thumbnail-link {
  display: inline-flex;
  flex-direction: column;
  justify-content: space-between;
  min-width: 334px;
  min-height: 191px;
  height: 100%;
  padding: 23px 23px 24px 24px;

  border-radius: 20px;
  vertical-align: middle;

  transition:
    box-shadow $trans-600-ease-in-out,
    background-color $trans-600-ease-in-out,
    color $trans-600-ease-in-out,
    border-color $trans-600-ease-in-out;

  &--theme-light {
    background-color: $color-default-white;
    color: $color-nero;

    border: 1px solid $color-pear;
    box-shadow: 0 10px 30px rgba($color: $color-night-rider, $alpha: 0.1);
  }

  &--theme-dark {
    background-color: $color-night-rider;
    color: $color-default-white;

    border: 1px solid rgba($color: $color-nobel-2, $alpha: 0.1);
    box-shadow: 0 10px 30px rgba($color: $color-night-rider, $alpha: 0.33);
  }

  @include hover {
    box-shadow: none;
    border: 1px solid $color-pear;

    background-color: $color-pear;
    color: $color-nero;

    & .thumbnail-link__icon {
      border-color: $color-default-white;
    }
  }

  &:active {
    transform: translate3d(2px, 2px, 0);
  }
}

.thumbnail-link__icon {
  position: relative;
  width: 68px;
  height: 68px;
  align-self: flex-end;

  border-radius: 12px;
  background-color: $color-pear;
  color: $color-night-rider;
  transition: border-color $trans-600-ease-in-out;

  &--theme-light {
    border: 2px solid $color-default-white;
    width: 68px;
    height: 68px;
  }

  &--theme-dark {
    border: 2px solid $color-pear;
  }
}

.thumbnail-link__icon svg {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate3d(-50%, -50%, 0);
}

.thumbnail-link__text {
  width: min-content;

  font-size: 28px;
  font-weight: 500;
  line-height: 1.1;
  letter-spacing: 0.01em;
  text-transform: uppercase;
}
