.user-catalog-form-2 {
  &__wrapper {
    max-width: 356px;
    padding: 40px 22px 60px 22px;

    background-color: $color-default-white;
    border: 2px solid $color-pear;
    border-radius: 0 20px 20px 0;
    box-shadow: 0 10px 30px rgba($color: $color-night-rider, $alpha: 0.33);
  }

  &__btnback {
    margin-bottom: 53px;
    padding-left: 0;

    font-weight: 500;
    font-size: 14px;
    line-height: 14px;
    word-break: break-word;
  }


  &__title {
    text-transform: uppercase;
    font-weight: 500;
    font-size: 18px;
    line-height: 18px;
    margin: 0 0 35px 0;
    color: $color-nero;
    letter-spacing: 0.01em;
  }

  &__form {
    display: flex;
    flex-direction: column;
  }

  &__title--sort {
    margin-bottom: 27px;
  }

  &__check-list {
    margin: 0;
    padding: 0;

    list-style: none;
  }

  &__block {
    margin-bottom: 30px;
  }

  &__block--location {
    margin-bottom: 38px;

    .user-catalog-form-2__block-title {
      margin-bottom: 25px;
    }

    .user-catalog-form-2__check-list-item {
      margin-bottom: 15px;
    }
  }

  &__block--type {
    margin-bottom: 53px;

    .user-catalog-form-2__block-title {
      margin-bottom: 25px;
    }

    .user-catalog-form-2__check-list {
      display: grid;
      grid-template-columns: 148px 150px;
      row-gap: 20px;
    }
  }

  &__block--level {
    margin-bottom: 54px;

    .user-catalog-form-2__block-title {
      margin-bottom: 26px;
    }

    .custom-toggle-radio__block {
      margin-bottom: 12px;
    }
  }

  &__block:last-of-type,
  &__check-list-item:last-child {
    margin-bottom: 0;
  }

  &__block-title {
    margin: 0 0 15px 0;

    font-weight: 500;
    font-size: 14px;
    line-height: 14px;
    color: $color-night-rider;
    text-transform: uppercase;
    letter-spacing: 0.02em;
  }

  &__btn-show {
    margin-top: 12px;
    padding-left: 0;
  }

  .custom-toggle {
    padding: 0;
  }

  .custom-toggle-radio {
    display: flex;
    flex-direction: column;
    padding: 0;
  }
}
