.create-training {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;

  &__wrapper {
    display: flex;
    flex-direction: column;
    width: 100%;
    margin-bottom: 25px;
  }

  &__block {
    margin-bottom: 35px;

    &:last-child {
      margin-bottom: 0;
    }
  }

  &__legend {
    display: block;
    margin: 0;
    margin-right: 20px;

    font-weight: 500;
    font-size: 20px;
    line-height: 20px;
    letter-spacing: 0.01em;
    text-transform: uppercase;

    color: $color-nero;
  }

  &__input {
    margin-top: 20px;

    label {
      input {
        padding: 17px 20px;
      }
    }
  }

  &__info {
    display: grid;
    grid-template-columns: 1fr 1fr;
    column-gap: 20px;
    row-gap: 25px;
    margin-top: 30px;
    margin-bottom: 35px;
  }

  &__label {
    display: block;
    margin-bottom: 20px;

    font-weight: 400;
    font-size: 16px;
    line-height: 16px;

    color: $color-dark-gray-3;
  }

  &__radio {
    flex-direction: row;
    column-gap: 20px;
    padding: 0;

    .custom-toggle-radio__block {
      margin-bottom: 0;
    }
  }

  &__textarea {
    margin-top: 20px;
  }

  &__drag-and-drop {
    margin-top: 20px;
  }

  &__button {
    max-width: 256px;
    margin-top: 20px;
  }
}
