.filter-price {
  //position: relative;

  display: grid;
  grid-template-columns: 151px 151px;
  column-gap: 10px;
  row-gap: 10px;

  input {
    max-width: 151px;
    padding: 13px 45px;

    border: 1px solid $color-very-light-grey;
    border-radius: 8px;
    outline: none;

    transition: 0.3s;

    @include focus {
      border: 1px solid $color-night-rider;
      box-shadow: 0 0 2px 2px $color-pear;
    }

    @include hover {
      border: 1px solid $color-night-rider;
    }
  }

  label {
    font-weight: 400;
    font-size: 16px;
    line-height: 16px;
    color: $color-dark-gray-2;
  }

  &__input-text {
    position: relative;
  }

  &__input-text--min {
    margin-right: 10px;
  }

  &__input-text--min label {
    position: absolute;
    bottom: 15px;
    left: 20px;
  }

  &__input-text--max label {
    position: absolute;
    bottom: 15px;
    left: 20px;
  }
}

.is-disabled {
  opacity: 0.3;
  pointer-events: none;
}
