.training-card {
  display: flex;
  flex-direction: column;
  border-radius: 20px;
  box-shadow: 0 10px 30px rgba($color-night-rider, 0.1);

  &--edit {
    .training-info__edit {
      &--edit {
        display: none;
      }

      &--save {
        display: flex;
      }
    }

    .training-info__discount {
      display: flex;
    }

    .training-video__button {
      display: none;
    }

    .training-video__edit-buttons {
      display: grid;
    }
  }
}
