.radiobuttons-block {
  &__title {
    margin: 0 0 15px 0;

    font-weight: 500;
    font-size: 20px;
    line-height: 20px;
    color: $color-night-rider;
    text-transform: uppercase;
    letter-spacing: 0.02em;
  }

  .custom-toggle-radio {
    flex-direction: row;
  }

  .custom-toggle-radio__block {
    margin-right: 20px;
  }

  .custom-toggle-radio__block:last-child {
    margin-right: 0;
  }
}
