.main-nav {
  display: flex;
  align-items: center;
  justify-content: center;

  &__list {
    display: flex;
    flex-wrap: wrap;
    column-gap: 60px;
    row-gap: 20px;
    margin: 0 40px;
    padding: 11px 46px;

    border: 2px solid $color-default-white;
    border-radius: 20px;

    list-style: none;

    @include vp-1279 {
      column-gap: 30px;
      min-width: 362px;
    }
  }

  &__item {
    position: relative;

    &--notifications {
      &.is-notifications {
        .main-nav__link::before {
          content: "";
          position: absolute;
          top: 2px;
          right: 4px;
          width: 8px;
          height: 8px;

          border-radius: 50%;
          background-color: $color-pear;
        }
      }

      &:hover,
      &:focus-within {
        .main-nav__dropdown {
          display: block;
        }
      }
    }
  }

  &__link {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 44px;
    height: 44px;

    border: 1px solid $color-transparent;
    border-radius: 50%;
    color: $color-dark-gray-2;

    transition: background-color $trans-default, color $trans-default;

    outline: none;

    &.is-active {
      background-color: $color-night-rider;
      color: $color-pear;

      pointer-events: none;
    }

    &.is-disabled {
      color: $color-night-rider;
      opacity: 0.15;

      user-select: none;
    }

    @include hover {
      color: $color-pear;
    }

    @include focus {
      border: 1px solid $color-nero;
    }
  }

  &__dropdown {
    z-index: 10;
    position: absolute;
    display: none;
    top: calc(100% + 35px);
    left: -25px;
    width: 374px;
    padding: 30px 10px 15px;

    border-radius: 16px;
    background-color: $color-default-white;
    box-shadow: 10px 10px 150px rgba($color-night-rider, 0.25);

    &::before {
      content: "";
      position: absolute;
      top: -8px;
      left: 40px;
      width: 16px;
      height: 16px;

      border-radius: 0 0 0 2px;
      background-color: $color-default-white;
      clip-path: polygon(0% 0%, 100% 100%, 0% 100%);
      transform: rotate(135deg);
    }

    &::after {
      content: "";
      position: absolute;
      top: -35px;
      left: 0;
      width: 100%;
      height: 35px;
    }
  }

  &__label {
    margin: 0 10px 10px;

    font-weight: 500;
    font-size: 16px;
    line-height: 16px;
    letter-spacing: 0.01em;
    text-transform: uppercase;

    color: $color-nero;
  }

  &__sublist {
    margin: 0;
    padding: 0;

    list-style: none;
  }

  &__subitem {
    width: 100%;
  }
}
