.custom-toggle {
  display: flex;
  padding: 10px 0;

  &__label {
    font-size: 16px;
    line-height: 16px;
    font-weight: 400;
    color: $color-night-rider;
  }

  label {
    display: flex;
    flex-direction: row;
    align-items: center;
    cursor: pointer;
  }

  input {
    position: absolute;

    width: 1px;
    height: 1px;
    margin: -1px;

    clip: rect(0 0 0 0);
  }

  input:checked ~ .custom-toggle__label {
    font-weight: 500;
  }

  &--checkbox {
    .custom-toggle__label {
      margin-left: 8px;
    }

    .custom-toggle__icon {
      width: 20px;
      height: 20px;

      border-radius: 2px;
    }

    input + .custom-toggle__icon {
      position: relative;
      cursor: pointer;
      padding: 0;

      @include active {
        box-shadow: 0 0 1px 1px $color-pear;
      }

      svg {
        display: none;
      }
    }

    input + .custom-toggle__icon::before {
      content: "";
      display: inline-block;
      width: 20px;
      height: 20px;
      border-radius: 2px;
      border: 1px solid $color-night-rider;
      background: $color-transparent;
      transition: $trans-default;
    }

    @include hover {
      input + .custom-toggle__icon::before {
        background: $color-white-smoke-2;
      }

      input:checked + .custom-toggle__icon::before {
        background: $color-night-rider;
      }
    }

    input:checked + .custom-toggle__icon::before {
      background: $color-night-rider;
    }

    input:disabled + .custom-toggle__icon {
      opacity: 0.3;
    }

    input:disabled + .custom-toggle__icon::before {
      opacity: 0.3;
    }

    input:checked + .custom-toggle__icon {
      svg {
        display: block;
        position: absolute;
        top: 7px;
        left: 5px;
        color: $color-pear;
        width: 9px;
        height: 6px;
      }
    }
  }

  &--switch {
    .custom-toggle__label {
      margin-left: 10px;
      font-weight: 500;
    }

    input + .custom-toggle__icon {
      position: relative;
      display: flex;
      cursor: pointer;
      padding: 0;

      transition: $trans-default;

      svg {
        display: none;
      }
    }

    input + .custom-toggle__icon::before {
      content: "";
      display: inline-block;
      width: 56px;
      height: 30px;
      border-radius: 16px;
      border: 1px solid $color-light-grey;
      background: $color-light-grey;
      transition: $trans-default;
      transform: translateZ(0);
    }

    input + .custom-toggle__icon::after {
      position: absolute;
      left: 2px;
      top: 2px;
      content: "";
      width: 26px;
      height: 26px;
      border-radius: 50px;
      border: 1px solid $color-light-grey;
      background: $color-white-smoke-4;
      transition: $trans-default;
      transform: translateZ(0);
    }

    @include hover {
      input + .custom-toggle__icon::after {
        background-color: $color-default-white;
        border: 1px solid $color-dark-gray-2;
      }

      input:checked + .custom-toggle__icon::after {
        background-color: $color-pear-2;
        border: 1px solid $color-pear-2;
      }
    }

    @include active {
      input + .custom-toggle__icon::before {
        box-shadow: 0 0 1px 1px $color-pear;
        border-color: $color-night-rider;
      }

      input:checked + .custom-toggle__icon::before {
        box-shadow: 0 0 1px 1px $color-night-rider;
        border-color: $color-pear;
      }
    }

    input:checked + .custom-toggle__icon::before {
      background-color: $color-night-rider;
    }

    input:checked + span::after {
      transform: translateX(100%);
      background-color: $color-pear;
      border: 1px solid $color-pear;
    }
  }

  &.is-invalid {
    label span {
      color: $color-venetian-red;
    }
  }

  .input-message {
    position: absolute;
    right: 0;
    bottom: -5px;

    &--invalid {
      color: $color-venetian-red;
    }

    &--valid {
      color: $color-pear;
    }
  }

  &--switch-right {
    label {
      flex-direction: row-reverse;
    }

    .custom-toggle__label {
      margin-left: 0;
      margin-right: 10px;
    }
  }
}

.custom-toggle--checkbox input {
  @include focus {
    & + .custom-toggle__icon::before {
      box-shadow: 0 0 1px 1px $color-pear;
      outline: none;
    }
  }
}

.custom-toggle--checkbox input:checked {
  @include focus {
    & + .custom-toggle__icon::before {
      box-shadow: 0 0 1px 1px $color-pear;
      outline: none;
    }
  }
}

.custom-toggle--switch input {
  @include focus {
    & + .custom-toggle__icon::before {
      box-shadow: 0 0 1px 1px $color-pear;
      border-color: $color-night-rider;
      outline: none;
    }
  }
}

.custom-toggle--switch input:checked {
  @include focus {
    & + .custom-toggle__icon::before {
      box-shadow: 0 0 1px 1px $color-night-rider;
      border-color: $color-pear;
      outline: none;
    }
  }
}

.is-disabled {
  opacity: 0.3;
  pointer-events: none;

  input + .custom-toggle__icon::after {
    background-color: $color-suva-grey;
  }

  input:checked + .custom-toggle__icon::after {
    background-color: $color-suva-grey;
    border-color: $color-suva-grey;
  }

  input:checked + .custom-toggle__icon::before {
    background-color: $color-very-light-grey;
    opacity: 0.8;
  }
}
