.payment-method {
  margin: 0 0 25px;

  &__title {
    font-size: 16px;
    line-height: 16px;
    font-weight: 400;
    color: $color-dark-gray-2;
    margin: 0 0 15px;
  }

  &__list {
    list-style: none;
    padding: 0;
    margin: 0;
    display: grid;
    grid-template-columns: repeat(3, 144px);
    gap: 10px;
  }

  &__item {
    margin: 0 0 10px 0;
    width: 144px;
    height: 80px;

    & img {
      height: 24px;
    }

    &:not(:last-child) {
      margin: 0 10px 10px 0;
    }
  }
}
