.price-service {
  color: $color-default-white;
  border: 1px solid rgba($color-nobel, 0.1);
  padding: 29px 57px;
  border-radius: 8px;
  margin: 0 0 10px;

  &__price {
    margin: 0;
    font-size: 18px;
    line-height: 18px;
    text-transform: uppercase;
    text-align: center;
    font-weight: 500;
  }
}
