.popup-form {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;

  display: flex;
  flex-direction: column;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;

  &__wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-shrink: 0;
    width: 100%;
    min-height: 100%;
    padding: 40px;
  }

  &__content {
    z-index: 10;
    position: relative;
    width: 688px;

    border: 1px solid $color-pear;
    border-radius: 60px;
    background-color: $color-default-white;
    box-shadow: 0 10px 100px rgba($color-night-rider, 0.1);
  }

  &__title-wrapper {
    padding: 43px 117px 42px;

    border-radius: 60px 60px 0 0;
    background-color: $color-night-rider;
  }

  &__title {
    margin: 0;

    font-weight: 700;
    font-size: 34px;
    line-height: 34px;
    letter-spacing: 0.01em;
    text-transform: uppercase;

    color: $color-default-white;
  }

  &__form {
    padding: 30px 117px 60px;
  }

  &--sign-in {
    .popup-form__content {
      width: 688px;
    }
  }

  &--sign-up {
    .popup-form__content {
      width: 922px;
    }
  }

  &--questionnaire-user,
  &--questionnaire-coach {
    .popup-form__content {
      width: 924px;
    }

    .popup-form__form {
      padding: 69px 117px;
    }
  }

  &--create-training {
    position: relative;

    .popup-form__wrapper {
      padding: 38px 0 60px;
    }

    .popup-form__content {
      width: 924px;
    }

    .popup-form__form {
      padding: 40px 117px 59px;
    }
  }

  &--map {
    position: absolute;
  }

  &--membership {
    position: absolute;
  }

  &--feedback {
    position: absolute;
  }

  &--buy {
    position: absolute;
  }
}
