.training-video {
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 1042px;
  height: 100%;
  padding: 59px;

  border: 1px solid $color-pear;
  border-top: none;

  border-radius: 0 0 20px 20px;
  background-color: $color-default-white;

  &--stop {
    .training-video__button {
      &--start {
        display: none;
      }

      &--stop {
        display: block;
      }
    }
  }

  &--load {
    .training-video__video {
      display: none;
    }

    .training-video__drop-files {
      display: flex;
    }
  }

  &__title {
    align-self: flex-start;

    margin: 0;

    font-weight: 700;
    font-size: 34px;
    line-height: 34px;
    letter-spacing: 0.01em;
    text-transform: uppercase;

    color: $color-nero;
  }

  &__video {
    position: relative;
    width: 100%;
    max-width: 922px;
    height: 100%;
    max-height: 560px;
    margin-top: 30px;
  }

  &__thumbnail {
    width: 100%;
    height: 100%;

    img {
      width: 100%;
      height: 100%;

      border: 1px solid $color-default-white;
      border-radius: 20px;

      object-fit: cover;
    }
  }

  &__play-button {
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    top: 250px;
    left: 50%;
    width: 80px;
    height: 80px;
    margin: -40px;
    padding: 25px 27px 25px 35px;

    border-radius: 50%;
    background-color: $color-night-rider;

    cursor: pointer;
    outline: none;

    transition: transform $trans-default, box-shadow $trans-default, background-color $trans-default;

    @include hover {
      transform: scale(1.1);
    }


    @include active {
      background-color: $color-nero;

      transform: scale(1);
    }

    @include focus {
      box-shadow: 0 0 2px 2px $color-pear;

      transform: scale(1);
    }

    svg {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 18px;
      height: 30px;

      color: $color-pear;
    }

    &.is-disabled {
      opacity: 1;
      background-color: $color-white-smoke-5;

      pointer-events: none;

      svg {
        color: rgba($color-night-rider, 0.15);
      }
    }
  }

  &__buttons-wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    row-gap: 20px;
    width: 100%;
    margin-top: 45px;
  }

  &__button {
    max-width: 256px;

    &--stop {
      display: none;
    }
  }

  &__drop-files {
    display: none;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    padding: 234px 0 348px;


    form {
      width: 100%;
    }
  }

  &__form-wrapper {
    display: grid;
    grid-template-columns: minmax(542px, 688px);
    justify-content: center;
    align-items: center;
    row-gap: 20px;
    width: 100%;
  }


  &__edit-buttons {
    display: none;
    grid-template-columns: 256px 256px;
    column-gap: 30px;
    row-gap: 20px;
  }
}
