.friends-list {
  &__wrapper {
    display: flex;
    flex-direction: column;
    padding-top: 18px;
    padding-bottom: 60px;
  }

  &__back {
    align-self: flex-start;

    margin-bottom: 30px;
  }

  &__title-wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 25px;
  }

  &__title {
    margin: 0;
    margin-right: 40px;

    font-weight: 700;
    font-size: 34px;
    line-height: 34px;
    letter-spacing: 0.01em;
    text-transform: uppercase;

    color: $color-nero;
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    column-gap: 20px;
    row-gap: 30px;
    margin: 0;
    margin-bottom: 60px;
    padding: 0;

    list-style: none;
  }

  &__item {
    width: 100%;
  }

  &__show-more {
    align-self: center;
  }
}
