.inner-page {
  .container {
    padding-left: 0;
  }

  &--no-sidebar {
    .container {
      padding: 0 22px;
    }

    .inner-page__wrapper {
      display: flex;
      flex-direction: column;
      padding: 18px 0 60px;
    }
  }

  &__wrapper {
    display: grid;
    grid-template-columns: 356px 1fr;
    column-gap: 20px;
    padding-bottom: 60px;
  }

  &__back {
    align-self: flex-start;
    margin-bottom: 40px;
  }

  &__content {
    width: 100%;
  }
}
