.my-progress {
  display: grid;
  grid-template-columns: 135px 1fr;
  width: 688px;
  overflow: hidden;

  border: 1px solid rgba($color-nobel-2, 0.1);
  border-radius: 20px;
  background-color: $color-night-rider;
  box-shadow: 0 10px 30px rgba($color-night-rider, 0.33);

  &__sidebar {
    position: relative;
    display: flex;
    flex-direction: column;
    padding-bottom: 22px;

    &::after {
      content: "";
      position: absolute;
      top: 0;
      right: 0;
      width: 1px;
      height: 100%;

      background-color: $color-default-white;
      opacity: 0.2;
    }
  }

  &__icon {
    width: 46px;
    height: 51px;
    margin: 29px 0 59px 19px;

    color: $color-pear;
  }

  &__list {
    margin: 0;
    padding: 0;

    list-style: none;
  }

  &__item {
    padding: 27px 19px;

    span {
      display: block;

      font-weight: 500;
      font-size: 12px;
      line-height: 13px;
      letter-spacing: 0.01em;
      text-transform: uppercase;

      color: $color-default-white;
    }
  }

  &__content {
    max-width: 553px;
    padding: 39px 20px;
  }

  &__title-wrapper {
    display: flex;
    justify-content: space-between;
    margin-bottom: 34px;
  }

  &__title {
    margin: 0;

    font-weight: 700;
    font-size: 28px;
    line-height: 28px;
    letter-spacing: 0.01em;
    text-transform: uppercase;

    color: $color-default-white;
  }

  &__controls {
    display: flex;
  }

  &__control {
    width: 30px;
    height: 30px;
    margin-right: 15px;

    &:last-child {
      margin-right: 0;
    }

    svg {
      color: $color-white-smoke-2;
    }
  }

  &__table {
    overflow: hidden;

    border-collapse: collapse;
  }

  &__row {
    display: grid;
    grid-template-columns: repeat(7, 114px);
    column-gap: 19px;
    row-gap: 19px;
    margin-bottom: 18px;
    padding-bottom: 17px;

    border-bottom: 1px dashed rgba($color-suva-grey, 0.3);

    &--head {
      margin-bottom: 20px;
      padding-bottom: 21px;
    }

    &:last-child {
      margin-bottom: 0;
      padding-bottom: 0;

      border-bottom: none;
    }
  }

  &__cell {
    padding: 14px;

    font-weight: 400;
    font-size: 14px;
    line-height: 14px;
    text-align: center;
    letter-spacing: 0.01em;

    border: 1px solid rgba($color-gainsboro, 0.1);
    border-radius: 8px;
    background-color: rgba($color-white-smoke-3, 0.02);
    color: $color-default-white;

    &--head {
      padding: 0;

      font-size: 12px;
      line-height: 12px;
      text-transform: uppercase;

      border: none;
      border-radius: unset;
      background-color: $color-transparent;
    }

    &--red {
      border: 1px solid $color-tall-poppy;
    }

    &--green {
      border: 1px solid $color-pear;
    }
  }
}
