.questionnaire-user {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;

  &__wrapper {
    display: flex;
    flex-direction: column;
    width: 100%;
    margin-bottom: 25px;
  }

  &__block {
    padding-bottom: 30px;
    margin-bottom: 30px;

    border-bottom: 1px dashed $color-very-light-grey;

    &:last-child {
      padding-bottom: 0;
      margin-bottom: 0;

      border-bottom: none;
    }
  }

  &__legend {
    display: block;
    margin-right: 20px;

    font-weight: 500;
    font-size: 20px;
    line-height: 20px;
    letter-spacing: 0.01em;
    text-transform: uppercase;

    color: $color-nero;
  }

  &__specializations {
    max-width: 540px;
    margin-top: 25px;
  }

  &__radio {
    flex-direction: row;
    column-gap: 20px;
    margin-top: 25px;
    padding: 0;

    .custom-toggle-radio__block {
      margin-bottom: 0;
    }
  }

  &__calories-lose {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 15px;
  }

  &__calories-waste {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__input {
    max-width: 216px;
  }

  &__button {
    max-width: 256px;
    margin-top: 20px;
  }
}
