.sort-for {
  display: flex;
  flex-direction: row;
  align-items: center;

  p {
    font-weight: 400;
    font-size: 16px;
    line-height: 16px;
    color: $color-suva-grey;
    margin: 0 20px 0 0;
  }

  .btn-filter-sort {
    margin-right: 20px;

    &:last-child {
      margin-right: 0;
    }
  }
}
