.personal-account-user {
  &__schedule {
    position: relative;
    margin-bottom: 20px;
    padding: 38px 60px;

    border-radius: 12px;
    background-color: $color-night-rider;

    &::before {
      content: "";
      position: absolute;
      top: 0;
      left: 50%;
      width: 1px;
      height: 100%;

      background-color: $color-default-white;
      opacity: 0.2;

      transform: translateX(-50%);
    }
  }

  &__form {
    display: grid;
    grid-template-columns: 1fr 1fr;
    column-gap: 120px;
  }

  &__input {
    display: flex;
    flex-direction: column;

    input {
      width: 100%;
      padding: 19px;

      font-weight: 700;
      font-size: 28px;
      line-height: 28px;
      letter-spacing: 0.01em;
      text-transform: uppercase;

      border: 1px solid rgba($color-nobel-2, 0.1);
      border-radius: 8px;
      background-color: rgba($color-white-smoke-3, 0.02);
      color: $color-default-white;
      outline: 0;

      transition: border $trans-default, box-shadow $trans-default;

      &:hover,
      &:active {
        border: 1px solid $color-default-white;
      }

      &:focus {
        border: 1px solid $color-default-white;
        box-shadow: 0 0 2px 2px $color-pear;
      }

      &:disabled {
        border: 1px solid rgba($color-nobel-2, 0.1);
        background-color: rgba($color-white-smoke-3, 0.02);

        pointer-events: none;
      }
    }
  }

  &__label {
    display: block;
    margin-bottom: 15px;

    font-weight: 400;
    font-size: 18px;
    line-height: 18px;
    letter-spacing: 0.01em;

    color: $color-default-white;
  }

  &__info {
    display: grid;
    grid-template-columns: 334px 1fr;
    column-gap: 20px;
    row-gap: 20px;
    margin-bottom: 20px;
  }

  &__my-progress {
    grid-column: 2 / 3;
    grid-row: 1 / 3;
  }

  &__diagram {
    display: none;
    width: 688px;
  }

  &__additional-info {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    column-gap: 20px;
    row-gap: 20px;
  }

  &__shop {
    grid-column: 2 / 3;
    grid-row: 1 / 3;
  }

  &__calendar {
    grid-column: 3 / 4;
    grid-row: 1 / 3;

    width: 334px;
    height: 402px;

    border-radius: 16px;
    background-color: $color-default-white;
  }
}
