.btn-icon {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 42px;
  height: 42px;

  color: $color-night-rider;

  background-color: $color-default-white;
  border: 1px solid transparent;
  border-radius: 50%;
  cursor: pointer;

  transition: color $trans-default, background-color $trans-default, border $trans-default, box-shadow $trans-default;

  &:disabled,
  &.is-disabled {
    color: rgba($color-night-rider, 0.3);
    background-color: #f0f0f0;
    opacity: 1;

    pointer-events: none;
  }

  @include hover {
    box-shadow: 0 3px 20px rgba(51, 51, 51, 0.1);
  }

  @include focus {
    box-shadow: 0 0 0 2px $color-pear;
    outline: none;
  }

  @include active {
    box-shadow: none;
    outline: none;
  }

  &--outlined {
    color: $color-white-smoke-2;
    background-color: transparent;
    border: 1px solid rgba($color-white-smoke-2, 0.4);

    &:disabled,
    &.is-disabled {
      color: rgba($color-white-smoke-2, 0.3);
      border: 1px solid rgba($color-white-smoke-2, 0.2);
      background-color: transparent;
      pointer-events: none;
    }

    @include hover {
      border: 1px solid $color-white-smoke-2;
    }

    @include focus {
      border: 1px solid rgba($color-white-smoke-2, 0.4);
      box-shadow: 0 0 0 2px $color-pear;
      outline: none;
    }

    @include active {
      box-shadow: none;
      outline: none;
    }
  }

  &--big {
    width: 58px;
    height: 58px;
  }

  &--quantity {
    height: 14px;
    width: 14px;
    padding: 0;
    border: 0;
    margin: 14px 0 0 0;
    background: $color-transparent;
    cursor: pointer;
    outline: 0;
    stroke: $color-default-black;
    transition: stroke $trans-default;

    @include hover-focus {
      stroke: $color-suva-grey;
    }

    @include active {
      stroke: $color-fuego;
    }
  }
}
