.popup-head {
  display: grid;
  grid-template-columns: 1fr min-content;
  box-shadow: 0 10px 30px rgba($color-night-rider, 0.1);
  background-color: $color-night-rider;
  color: $color-default-white;
  padding: 29px 40px 30px 117px;
  align-items: center;
  border-radius: 60px 60px 0 0;

  &__header {
    padding: 0;
    margin: 0 40px 0 0;
    font-weight: 700;
    font-size: 34px;
    line-height: 34px;
    letter-spacing: 0.01em;
    text-transform: uppercase;
  }

  &__address {
    display: grid;
    grid-template-columns: min-content 1fr;
    align-items: center;
    margin: 15px 0 0;
    padding: 0;
    font-weight: 400;
    font-size: 14px;
    line-height: 14px;
    color: $color-dark-gray;

    span {
      padding: 0 0 0 8px;
    }
  }

  &--address {
    grid-template-columns: max-content 1fr min-content;
  }
}
