.user-card-coach {
  &__card {
    display: grid;
    margin-bottom: 64px;

    background-color: $color-default-white;
    border: 1px solid $color-pear;
    border-radius: 12px;

    grid-template-columns: 1fr calc(58% - 4px);
  }

  &__content {
    padding: 96px 116px 96px 118px;
  }

  &__head {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    flex-wrap: wrap;
    align-items: center;
    margin-bottom: 12px;
    padding-right: 5px;
  }

  &__title {
    margin: 0;

    font-style: bold;
    font-weight: 700;
    font-size: 34px;
    line-height: 34px;
    color: $color-night-rider;
    text-transform: uppercase;
    letter-spacing: 1%;
  }

  &__icon {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 32px;
    height: 32px;

    background-color: $color-night-rider;
    border-radius: 100px;
  }

  &__crown {
    color: $color-pear;
  }

  &__label {
    display: flex;
    flex-direction: row;
    align-items: center;
    margin-bottom: 22px;

    font-weight: 400;
    font-size: 14px;
    line-height: 14px;
    color: $color-dark-gray;

    span {
      padding-left: 8px;
    }

    svg {
      flex-shrink: 0;
    }
  }

  &__status-container {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    margin-bottom: 30px;
  }

  &__status {
    position: relative;

    display: inline-flex;
    justify-content: center;
    align-items: center;
    padding: 12px 20px 12px 34px;
    margin-bottom: 5px;

    font-weight: 400;
    font-size: 12px;
    line-height: 12px;
    color: $color-night-rider;
    text-transform: uppercase;

    border: 1px solid $color-pear;
    border-radius: 100px;
  }

  &__status--check {
    &::after {
      content: "";
      position: absolute;
      top: 15px;
      left: 20px;

      width: 8px;
      height: 8px;

      background-color: $color-pear;
      border-radius: 100px;
    }
  }

  &__status--tag {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    margin-right: 15px;
    padding: 12px 20px;

    font-weight: 400;
    font-size: 12px;
    line-height: 12px;
    color: $color-default-white;
    text-transform: uppercase;

    background-color: $color-night-rider;
    border: 1px solid $color-pear;
    border-radius: 100px;
  }

  &__icon-cup {
    margin-right: 6px;

    color: $color-pear;
  }

  &__text {
    margin-bottom: 25px;
    padding-top: 35px;

    border-top: 1px solid rgba($color-nobel, 0.2);

    p {
      max-width: 354px;
      margin: 0;
      margin-bottom: 15px;

      font-weight: 400;
      font-size: 16px;
      line-height: 22px;
      color: $color-night-rider;
    }

    p:last-of-type {
      margin-bottom: 0;
    }
  }

  &__sertificate {
    padding: 0;
  }

  &__hashtag-list {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    margin: 0;
    margin-top: 24px;
    margin-bottom: 30px;
    padding: 0;

    list-style: none;
  }

  &__hashtag-item {
    margin-right: 8px;
    margin-bottom: 10px;
  }

  &__hashtag-item:last-child {
    margin-right: 0;
  }

  &__btn {
    margin-top: 10px;
  }

  &__gallary {
    padding: 99px 59px;

    background-color: $color-night-rider;
    border-top-right-radius: 12px;
    border-bottom-right-radius: 12px;
  }

  &__gallary-list {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 20px;
    margin: 0;
    padding: 0;

    list-style: none;
  }

  &__gallary-item {
    border-radius: 8px;

    img {
      border-radius: 8px;
      height: 573px;
      width: 334px;
      object-fit: cover;
    }
  }

  &__gallary-item:last-child {
    margin-right: 0;
  }

  &__training-head {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin-bottom: 26px;
  }

  &__training-title {
    margin: 0;

    font-style: bold;
    font-weight: 700;
    font-size: 34px;
    line-height: 34px;
    color: $color-night-rider;
    text-transform: uppercase;
    letter-spacing: 1%;
  }

  &__training-bts {
    display: flex;
    flex-direction: row;
  }

  &__training-btn:first-of-type {
    margin-right: 15px;
  }

  &__training-list {
    margin: 0;
    padding: 0;
    display: flex;
    flex-direction: row;
    list-style: none;
    margin-bottom: 60px;
  }

  &__training-item {
    margin-right: 20px;
  }

  &__training-item:last-child {
    margin-right: 0;
  }

  &__training-form {
    display: flex;
    flex-direction: column;
    max-width: 435px;
    margin: 0 auto;

    .btn {
      max-width: 374px;
      margin: 0 auto;
    }
  }

  &__training-check {
    margin-top: 10px;
    margin-left: 5px;
  }

  .custom-toggle {
    font-weight: 500;
    font-size: 16px;
    line-height: 16px;
  }
}
