.user-catalog-form {
  &__wrapper {
    max-width: 356px;
    padding: 42px 22px 60px 20px;

    background-color: $color-default-white;
    border: 2px solid $color-pear;
    border-radius: 0 20px 20px 0;
    box-shadow: 0 10px 30px rgba($color: $color-night-rider, $alpha: 0.33);
  }

  &__btnback {
    margin-bottom: 53px;
    padding-left: 0;

    font-weight: 500;
    font-size: 14px;
    line-height: 14px;
    word-break: break-word;
  }


  &__title {
    text-transform: uppercase;
    font-weight: 500;
    font-size: 18px;
    line-height: 18px;
    letter-spacing: 0.01em;
    margin: 0 0 35px 0;
    color: $color-nero;
  }

  &__form {
    display: flex;
    flex-direction: column;
  }

  &__title--sort {
    margin-bottom: 25px;
  }

  &__check-list {
    margin: 0;
    padding: 0;

    list-style: none;
  }

  &__block--location {
    margin-bottom: 32px;

    .user-catalog-form__block-title {
      margin-bottom: 25px;
    }

    .user-catalog-form__check-list-item {
      margin-bottom: 15px;
    }
  }

  &__block--spezialization {
    margin-bottom: 34px;

    .user-catalog-form__check-list-item {
      margin-bottom: 15px;
    }
  }

  &__block--level {
    margin-bottom: 25px;

    .user-catalog-form__check-list-item {
      margin-bottom: 20px;
    }
  }

  &__block:last-of-type,
  &__check-list-item:last-child {
    margin-bottom: 0;
  }

  &__block-title {
    margin: 0 0 25px 0;

    font-weight: 500;
    font-size: 14px;
    line-height: 14px;
    color: $color-night-rider;
    text-transform: uppercase;
    letter-spacing: 0.02em;
  }

  &__btn-show {
    margin-top: 12px;
    padding-left: 0;
  }

  .custom-toggle,
  .custom-toggle-radio {
    padding: 0;
  }

  .custom-toggle-radio {
    display: flex;
    flex-direction: column;
  }

  .custom-toggle-radio__block {
    margin-bottom: 13px;
  }
}
