.sign-up {
  display: flex;
  flex-direction: column;
  align-items: center;

  &__load-photo {
    align-self: flex-start;

    display: flex;
    align-items: center;
    margin-bottom: 30px;
  }

  &__description {
    display: flex;
    flex-direction: column;
    margin-left: 20px;
  }

  &__legend {
    margin: 0;

    font-weight: 500;
    font-size: 20px;
    line-height: 20px;
    letter-spacing: 0.01em;
    text-transform: uppercase;

    color: $color-nero;
  }

  &__text {
    display: block;
    margin-top: 8px;

    font-weight: 400;
    font-size: 14px;
    line-height: 14px;

    color: $color-dark-gray-3;
  }

  &__data {
    display: grid;
    grid-template-columns: 1fr 1fr;
    column-gap: 20px;
    row-gap: 30px;
    width: 100%;
    margin-bottom: 30px;
  }

  &__radio {
    .custom-toggle-radio {
      flex-direction: row;
      justify-content: flex-start;
      column-gap: 15px;
      row-gap: 5px;
      padding: 0;
    }

    .custom-toggle-radio__block {
      margin-bottom: 0;
    }
  }

  &__label {
    display: block;
    margin-bottom: 25px;

    font-weight: 400;
    font-size: 16px;
    line-height: 16px;

    color: $color-dark-gray-3;
  }

  &__role {
    width: 100%;
    margin-bottom: 25px;
  }

  &__role-selector {
    margin-top: 15px;
  }

  &__checkbox {
    align-self: flex-start;

    position: relative;
    display: flex;
    margin-bottom: 25px;


    input {
      position: absolute;

      width: 1px;
      height: 1px;
      margin: -1px;

      clip: rect(0 0 0 0);
    }

    input:checked ~ .sign-up__checkbox-label {
      font-weight: 500;
    }

    input:checked + .sign-up__checkbox-icon {
      &::before {
        background-color: $color-night-rider;
      }

      svg {
        display: block;
        position: absolute;
        content: "";
        top: 50%;
        left: 50%;

        color: $color-pear;

        transform: translate(-50%, -50%);

        transition: background-color 0.3s ease;
      }
    }

    input:focus-visible + .sign-up__checkbox-icon {
      box-shadow: 0 0 1px 1px $color-pear;
    }

    input:active + .sign-up__checkbox-icon {
      box-shadow: 0 0 1px 1px $color-pear;
    }

    label {
      position: relative;

      padding: 2px 0 3px 32px;

      cursor: pointer;

      span {
        margin-bottom: 0;
        margin-left: 0;
      }
    }
  }

  &__checkbox-icon {
    position: absolute;
    top: 0;
    left: 0;

    width: 24px;
    height: 24px;

    border: 1px solid $color-night-rider;
    border-radius: 2px;

    transition: border 0.3s ease;

    svg {
      display: none;
    }

    &::before {
      content: "";
      position: absolute;
      top: 50%;
      left: 50%;

      width: 24px;
      height: 24px;
      border-radius: 2px;

      background-color: $color-transparent;

      transform: translate(-50%, -50%);

      transition: background-color 0.3s ease;
    }

    &:hover {
      background-color: $color-white-smoke-2;
    }

    &:active {
      box-shadow: 0 0 1px 1px $color-pear;
    }
  }

  &__checkbox-label {
    font-weight: 400;
    font-size: 12px;
    line-height: 12px;

    color: $color-night-rider;

    span {
      text-decoration: underline;
      text-underline-offset: 2px;
    }
  }

  &__button {
    max-width: 256px;
    margin-top: 20px;
  }
}
