.special-for-you {
  position: relative;
  overflow: hidden;

  &__wrapper {
    padding-top: 48px;
    padding-bottom: 70px;
  }

  &__title-wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 30px;
  }

  &__title {
    margin: 0;
    margin-right: 20px;

    font-weight: 700;
    font-size: 34px;
    line-height: 34px;
    letter-spacing: 0.01em;
    text-transform: uppercase;

    color: $color-nero;
  }

  &__controls {
    display: flex;
  }

  &__control {
    margin-right: 15px;

    &:last-child {
      margin-right: 0;
    }
  }

  &__list {
    display: flex;
    margin: 0;
    padding: 0;

    list-style: none;
  }

  &__item {
    margin-right: 20px;

    &:last-child {
      margin-right: 0;
    }
  }
}
