.thumbnail-friend {
  min-width: 452px;
  width: min-content;

  border: 1px solid $color-pear;
  box-shadow: 0 10px 30px rgba($color: $color-night-rider, $alpha: 0.33);
  border-radius: 20px;
  overflow: hidden;
  transition: box-shadow $trans-600-ease-in-out;

  @include hover {
    box-shadow: none;
  }
}

.thumbnail-friend__info {
  display: grid;
  grid-template-columns: 78px 1fr min-content;
  grid-template-rows: min-content auto;
  row-gap: 19px;
  align-items: start;

  padding: 30px 18px 27px 20px;

  &--theme-light {
    background-color: $color-default-white;
    color: $color-night-rider;
  }

  &--theme-dark {
    background-color: $color-night-rider;
    color: $color-default-white;
  }
}

.thumbnail-friend__image-status {
  position: relative;

  grid-column: 1 / 2;
  grid-row: 1 / 2;

  width: 78px;
  height: 78px;
}

.thumbnail-friend__image {
  width: 100%;
  height: 100%;

  border-radius: 50%;
  overflow: hidden;
}

.thumbnail-friend__image img {
  max-width: 100%;
  height: auto;
  object-fit: cover;
}

.thumbnail-friend__online-status {
  position: absolute;
  right: 6px;
  bottom: 4px;

  width: 12px;
  height: 12px;

  border-radius: 50%;
  z-index: 1;

  &--is-online {
    background-color: $color-pear;
  }

  &--is-offline {
    background-color: $color-alizarin;
  }
}

.thumbnail-friend__header {
  grid-column: 2 / 3;
  grid-row: 1 / 2;

  display: grid;
  grid-template-columns: 1fr;
  row-gap: 4px;
  margin-bottom: 5px;
  margin-left: 20px;
  margin-top: 18px;
}

.thumbnail-friend__name {
  grid-column: 1 / 2;
  grid-row: 1 / 2;

  margin: 0;

  font-size: 22px;
  font-weight: 500;
  line-height: 1;
  letter-spacing: 0.01em;
  text-transform: uppercase;
}

.thumbnail-friend__location {
  grid-column: 1 / 3;
  grid-row: 2 / 3;

  display: flex;
  align-items: center;

  font-size: 14px;
  line-height: 1;
  color: $color-dark-grey;
}

.thumbnail-friend__location svg {
  flex: 0 0 14px;
  width: 14px;
  height: 16px;
  align-self: flex-start;
}

.thumbnail-friend__location-address {
  margin-left: 7px;

  font-style: normal;
}

.thumbnail-friend__training-types-list {
  grid-column: 3 / 4;
  grid-row: 1 / 2;
  justify-self: end;

  margin: 0;
  padding: 0;
  list-style-type: none;

  margin-top: 15px;

  display: grid;
  grid-template-columns: 1fr;
  align-content: start;
  justify-content: end;
  row-gap: 6px;
}

.thumbnail-friend__hashtag {
  padding: 5px 10px;

  font-size: 12px;
  line-height: 1;

  color: inherit;
  border-color: $color-very-light-grey;
}

.thumbnail-friend__activity-bar {
  grid-column: 1 / 4;
  grid-row: 2 / 3;

  display: flex;
  flex-wrap: wrap;
}

.thumbnail-friend__ready-status {
  position: relative;

  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 15px 26px 15px 41px;
  margin-bottom: 2px;
  margin-right: 5px;

  font-size: 12px;
  line-height: 1;
  text-transform: uppercase;
  color: inherit;

  vertical-align: middle;

  border-radius: 49px;

  &::before {
    position: absolute;
    left: 24px;
    top: 50%;
    transform: translate3d(0, -50%, 0);

    width: 8px;
    height: 8px;

    border-radius: 50%;

    content: "";
  }

  &--is-ready {
    border: 1px solid $color-pear;

    &::before {
      background-color: $color-pear;
    }
  }

  &--is-not-ready {
    color: $color-dark-gray;

    border: 1px solid $color-very-light-grey;

    &::before {
      background-color: $color-very-light-grey;
    }
  }
}

.thumbnail-friend__invite-button {
  width: 43px;
  height: 46px;
  padding: 0;
  margin-left: 10px;

  border: 0;
  background-color: transparent;
  cursor: pointer;

  color: $color-night-rider;
  transition: color $trans-400-ease-in-out;

  &:first-of-type {
    margin-left: auto;
  }

  @include hover {
    color: $color-pear;
  }

  @include focus {
    outline: 2px solid $color-pear;
    outline-offset: 3px;
  }

  &:active {
    transform: scale(0.95);
  }

  &:disabled,
  &.is-disabled {
    color: $color-light-grey;

    cursor: default;
    pointer-events: none;
  }
}

.thumbnail-friend__invite-button svg {
  width: 100%;
  height: 100%;
}

.thumbnail-friend__request-status {
  display: flex;
  flex-direction: column;
  padding: 25px 20px 24px;

  background-color: $color-night-rider;
  color: $color-default-white;

  &--role-user {
    border-top: 1px solid $color-pear;
  }

  &--role-coach {
    border-top: 1px solid rgba($color: $color-default-white, $alpha: 0.2);
  }
}

.thumbnail-friend__request-text {
  margin: 0;
  margin-bottom: 16px;

  font-size: 12px;
  font-weight: 500;
  line-height: 1;
  letter-spacing: 0.03em;
  text-transform: uppercase;
  color: $color-default-white;

  &:last-child {
    margin-bottom: 0;
  }
}

.thumbnail-friend__button-wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 12px;
}

.thumbnail-friend__button {
  padding: 16px 15px;
}
