.thumbnail-preview {
  position: relative;
  display: flex;
  flex-direction: column;
  min-width: 452px;
  min-height: 291px;
  height: 100%;
  width: min-content;

  border: 1px solid $color-pear;
  box-shadow: 0 10px 30px rgba($color: $color-night-rider, $alpha: 0.33);
  border-radius: 20px;
  overflow: hidden;
  transition: box-shadow $trans-600-ease-in-out;

  @include hover {
    box-shadow: none;

    & .thumbnail-preview__image {
      filter: grayscale(0);
    }
  }
}

.thumbnail-preview__image {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;

  border-radius: 20px;
  overflow: hidden;

  z-index: -1;
  filter: grayscale(1);
  transition: filter $trans-600-ease-in-out;
}

.thumbnail-preview__image img {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.thumbnail-preview__inner {
  flex-grow: 1;
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  width: 100%;
  padding: 15px 19px;
}

.thumbnail-preview__title {
  margin: 0;
  margin-bottom: 4px;

  font-size: 28px;
  font-weight: 500;
  line-height: 1;
  letter-spacing: 0.01em;
  text-transform: uppercase;
  color: $color-default-white;
}

.thumbnail-preview__button-wrapper {
  display: flex;
  flex-direction: column-reverse;
}

.thumbnail-preview__button {
  width: 140px;
  margin-top: 10px;
  line-height: 1;

  &:last-child {
    margin-top: 0;
  }
}
