.personal-account-coach {
  &__navigation {
    display: grid;
    grid-template-columns: repeat(3, minmax(200px, 334px));
    column-gap: 20px;
    row-gap: 20px;
    margin-bottom: 50px;
  }

  &__calendar {
    grid-column: 3 / 4;
    grid-row: 1 / 3;

    width: 334px;
    height: 402px;

    border-radius: 16px;
    background-color: $color-default-white;
  }

  &__additional-info {
    width: 100%;
  }

  &__label-wrapper {
    display: flex;
    align-items: center;
    margin-bottom: 30px;
  }

  &__label {
    margin: 0;
    margin-right: 43px;

    font-weight: 500;
    font-size: 22px;
    line-height: 22px;
    letter-spacing: 0.01em;
    text-transform: uppercase;

    color: $color-nero;
  }

  &__button {
    margin-right: 43px;
  }

  &__controls {
    display: flex;
    margin-left: auto;
  }

  &__control {
    margin-right: 15px;

    &:last-child {
      margin-right: 0;
    }
  }

  &__list {
    display: flex;
    max-width: 1042px;
    margin: 0;
    padding: 0;
    overflow: hidden;

    list-style: none;
  }

  &__item {
    margin-right: 20px;

    &:last-child {
      margin-right: 0;
    }
  }
}
