.services-check {
  margin: 0 0 24px;

  &__title {
    margin: 0 0 15px;
    font-size: 14px;
    line-height: 14px;
    font-weight: 500;
    text-transform: uppercase;
    letter-spacing: 0.02em;
  }

  &__list {
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;
    flex-wrap: wrap;
  }

  &__item:not(:last-child) {
    margin: 0 13px 0 0;
  }
}
