.filter-range {
  &__scale {
    width: 312px;
    height: 1px;

    background-color: $color-dark-gray-2;
  }

  &__bar {
    width: 208px;
    height: 1px;

    background-color: $color-night-rider;
  }

  &__control {
    position: relative;

    display: flex;
    flex-direction: row;
  }

  &__min-toggle,
  &__max-toggle {
    width: 16px;
    height: 16px;

    background-color: $color-night-rider;
    border-radius: 50%;

    transition: $trans-default;
    border: none;
    cursor: pointer;

    @include hover {
      background-color: $color-dark-grey;
    }

    @include active {
      box-shadow: 0 0 2px 2px $color-pear;
    }
  }

  &__min-toggle {
    position: absolute;
    top: -9px;
  }

  &__max-toggle {
    position: absolute;
    top: -9px;
    left: 208px;
  }
}
