.total {
  min-width: 320px;
  padding: 18px;

  border: 2px solid $color-pear;
  border-radius: 12px;
  background-color: $color-night-rider;

  &__title-wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 44px;
  }

  &__title {
    margin: 0;
    margin-right: 20px;

    font-weight: 400;
    font-size: 16px;
    line-height: 16px;
    letter-spacing: 0.01em;
    text-transform: uppercase;

    color: $color-default-white;
  }

  &__icon {
    flex-shrink: 0;

    width: 30px;
    height: 30px;

    color: $color-pear;
  }

  &__result {
    display: flex;
    margin: 0;
  }

  &__item {
    margin-right: 80px;

    &:last-child {
      margin-right: 0;
    }
  }

  &__label {
    margin-bottom: 10px;

    font-weight: 400;
    font-size: 14px;
    line-height: 14px;

    color: $color-very-light-grey;
  }

  &__number {
    margin: 0;

    font-weight: 700;
    font-size: 32px;
    line-height: 32px;
    letter-spacing: 0.01em;
    text-transform: uppercase;

    color: $color-default-white;
  }
}
