.popular-trainings {
  position: relative;
  overflow: hidden;

  &__wrapper {
    padding-bottom: 60px;
  }

  &__title-wrapper {
    display: flex;
    align-items: center;
    margin-bottom: 30px;
  }

  &__title {
    margin: 0;
    margin-right: 40px;

    font-weight: 700;
    font-size: 34px;
    line-height: 34px;
    letter-spacing: 0.01em;
    text-transform: uppercase;
  }

  &__button {
    flex-shrink: 0;

    margin-right: 40px;
  }

  &__controls {
    display: flex;
    margin-left: auto;
  }

  &__control {
    margin-right: 15px;

    &:last-child {
      margin-right: 0;
    }
  }

  &__list {
    display: flex;
    margin: 0;
    padding: 0;

    list-style: none;
  }

  &__item {
    margin-right: 20px;

    &:last-child {
      margin-right: 0;
    }
  }
}
