.user-info {
  width: 356px;
  padding: 44px 21px;

  background-color: $color-default-white;
  border: 1px solid $color-pear;
  border-radius: 0 20px 20px 0;
  box-shadow: 0 10px 150px rgba(51, 51, 51, 0.15);
}

.user-info__header {
  display: flex;
  justify-content: space-between;
  margin-bottom: 29px;
}

.user-info__form {
  position: relative;
}

.user-info__edit-button {
  position: absolute;
  top: 2px;
  right: 0;
}

.user-info__save-button {
  position: absolute;
  top: 2px;
  right: 31px;
}

.user-info__section {
  margin-bottom: 37px;
}

.user-info__title {
  margin: 0 0 23px;

  font-weight: 500;
  font-size: 18px;

  text-transform: uppercase;
  color: #181818;

  &--status {
    margin: 0 0 20px;
  }

  &--specialization {
    margin: 0 0 24px;
  }
}

.user-info__input {
  margin-bottom: 19px;
}

.user-info__textarea textarea {
  height: 147px;
}

.user-info__toggle {
  padding: 0;
}

.user-info__specialization {
  gap: 8px;

  span {
    padding: 5px 15px;
    font-size: 14px;
  }
}

.user-info__select:not(:last-child) {
  margin-bottom: 18px;
}
