.btn-show-more {
  display: flex;
  flex-direction: row;
  align-items: center;
  padding-bottom: 4px;
  padding-left: 0;
  padding-right: 0;

  background-color: $color-transparent;
  border: none;
  border-bottom: 2px solid $color-pear;
  outline: none;
  cursor: pointer;

  transition: $trans-default;

  span {
    margin-right: 8px;

    font-weight: 500;
    font-size: 14px;
    line-height: 14px;
    color: $color-night-rider;

    transition: $trans-default;
  }

  svg {
    width: 15px;
    height: 8px;

    color: $color-night-rider;
  }

  @include hover {
    border-color: $color-dark-grey;

    span,
    svg {
      opacity: 0.8;
    }
  }

  @include focus {
    box-shadow: 0 0 2px 2px $color-pear;
  }

  @include active {
    border-bottom: 2px solid $color-pear;
  }
}

.is-disabled {
  opacity: 0.3;
  pointer-events: none;
}
