.user-info-edit {
  width: 356px;
  padding: 44px 21px;

  background-color: $color-default-white;
  border: 1px solid $color-pear;
  border-radius: 0 20px 20px 0;
  box-shadow: 0 10px 150px rgba(51, 51, 51, 0.15);
}

.user-info-edit__header {
  display: flex;
  justify-content: space-between;
  margin-bottom: 29px;
}

.user-info-edit__controls {
  display: flex;
  gap: 28px;
}

.user-info-edit__control-btn {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 36px;
  height: 36px;

  background: $color-default-white;
  border: 1px solid $color-default-black;
  border-radius: 20px;

  transition: box-shadow $trans-default;

  @include hover {
    box-shadow: 0 3px 20px rgba(51, 51, 51, 0.1);
    cursor: pointer;
  }

  @include focus {
    box-shadow: 0 0 0 2px $color-pear;
    outline: none;
  }

  @include active {
    box-shadow: none;
    outline: none;
  }
}

.user-info-edit__form {
  position: relative;
}

.user-info-edit__save-button {
  position: absolute;
  top: 2px;
  right: 31px;
}

.user-info-edit__section {
  margin-bottom: 37px;
}

.user-info-edit__title {
  margin: 0 0 23px;

  font-weight: 500;
  font-size: 18px;

  text-transform: uppercase;
  color: #181818;

  &--status {
    margin: 0 0 20px;
  }

  &--specialization {
    margin: 0 0 24px;
  }
}

.user-info-edit__input {
  margin-bottom: 19px;
}

.user-info-edit__textarea textarea {
  height: 147px;
}

.user-info-edit__toggle {
  padding: 0;
}

.user-info-edit__specialization {
  gap: 8px;

  span {
    padding: 5px 15px;
    font-size: 14px;
  }
}

.user-info-edit__select:not(:last-child) {
  margin-bottom: 18px;
}
