.custom-toggle-radio {
  font-size: 16px;
  line-height: 16px;
  font-weight: 400;

  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  padding: 10px 0;

  &__block {
    margin-bottom: 15px;
  }

  &__block:last-child {
    margin-bottom: 0;
  }

  label {
    display: inline-flex;
    flex-direction: row;
    align-items: center;
    cursor: pointer;

    @include hover {
      input + .custom-toggle-radio__icon::before {
        background: $color-white-smoke-2;
      }

      input:checked + .custom-toggle-radio__icon::before {
        background: $color-night-rider;
      }
    }
  }

  input {
    position: absolute;

    width: 1px;
    height: 1px;
    margin: -1px;

    clip: rect(0 0 0 0);

    @include focus {
      & + .custom-toggle-radio__icon::before {
        box-shadow: 0 0 1px 1px $color-pear;
        outline: none;
      }
    }
  }

  .custom-toggle-radio__label {
    margin-left: 8px;
  }

  &--big {
    .custom-toggle-radio__icon {
      width: 22px;
      height: 22px;
    }

    input:checked + .custom-toggle-radio__icon {
      width: 22px;
      height: 22px;
    }
  }

  &__icon {
    position: absolute;
    top: 0;
    left: 0;
  }

  input + .custom-toggle-radio__icon {
    position: relative;
    cursor: pointer;
    padding: 0;
    width: 20px;
    height: 20px;
    border-radius: 50px;

    @include active {
      box-shadow: 0 0 1px 1px $color-pear;
    }
  }

  input + .custom-toggle-radio__icon::before {
    content: "";
    display: inline-block;
    width: 20px;
    height: 20px;
    border-radius: 50px;
    border: 1px solid $color-night-rider;
    background: $color-transparent;
    transition: $trans-default;
  }

  input:checked + .custom-toggle-radio__icon::before {
    background: $color-night-rider;
  }

  input:checked + .custom-toggle-radio__icon::after {
    background: $color-pear;
    width: 6px;
    height: 6px;
    content: "";
    position: absolute;
    border-radius: 50px;
    top: 7px;
    left: 7px;
  }

  input:checked ~ .custom-toggle-radio__label {
    font-weight: 500;
  }
}

.is-disabled {
  opacity: 0.3;
  pointer-events: none;
}
