.btn-filter-sort {
  display: inline-flex;
  flex-direction: row;
  align-items: center;
  padding: 0;

  font-weight: 400;
  font-size: 16px;
  line-height: 16px;
  color: $color-suva-grey;

  background: $color-transparent;
  border: none;
  outline: none;
  cursor: pointer;

  transition: $trans-default;

  &:focus {
    color: $color-night-rider;
    font-weight: 500;
    opacity: 1;
  }

  span {
    margin-right: 5px;
  }

  @include focus {
    color: $color-night-rider;
    font-weight: 500;
  }

  @include hover {
    opacity: 0.8;
  }
}
