.drag-and-drop {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  max-width: 688px;

  @include hover {
    .drag-and-drop__label {
      border: 1px solid $color-night-rider;
    }
  }

  @include active {
    .drag-and-drop__label {
      border: 1px solid $color-night-rider;
      background-color: $color-white-smoke-3;
    }
  }

  @include focus {
    .drag-and-drop__label {
      border: 1px solid $color-night-rider;
      box-shadow: 0 0 2px 2px $color-pear;
    }
  }

  &.is-disabled {
    .drag-and-drop__label {
      border: 1px solid $color-nero;
      background-color: rgba($color-dark-gray-2, 0.08);
    }
  }

  label {
    width: 100%;
  }

  svg {
    width: 20px;
    height: 20px;
    margin-left: 20px;

    color: $color-night-rider;
  }

  input {
    position: absolute;

    width: 1px;
    height: 1px;
    margin: -1px;

    clip: rect(0 0 0 0);
  }

  &__label {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    padding: 16px 19px;

    font-weight: 400;
    font-size: 16px;
    line-height: 16px;

    color: $color-dark-gray-2;

    cursor: pointer;

    border: 1px solid $color-very-light-grey;
    border-radius: 8px;

    transition: border $trans-default, background-color $trans-default, box-shadow $trans-default;
  }

  &__icon {
    width: 20px;
    height: 20px;
  }
}
