.food-diary {
  &__wrapper {
    display: flex;
    flex-direction: column;
  }

  &__title {
    margin: 0;
    margin-bottom: 35px;

    font-weight: 700;
    font-size: 34px;
    line-height: 34px;
    letter-spacing: 0.01em;
    text-transform: uppercase;

    color: $color-nero;
  }

  &__block {
    display: grid;
    grid-template-columns: minmax(152px, 216px) 1fr;
    column-gap: 20px;
    margin-bottom: 35px;
  }

  &__sidebar {
    display: flex;
    flex-direction: column;

    border-radius: 12px;
    background-color: $color-night-rider;
  }

  &__icon {
    width: 21px;
    height: 18px;
    margin: 21px 0 21px 20px;

    color: $color-pear;
  }

  &__list {
    margin: 0;
    padding: 0;

    list-style: none;
  }

  &__item {
    padding: 49px 25px 48px;

    border-bottom: 1px dashed rgba($color-suva-grey, 0.3);

    &:last-child {
      border-bottom: none;
    }

    span {
      display: block;

      font-weight: 500;
      font-size: 18px;
      line-height: 18px;
      letter-spacing: 0.01em;
      text-transform: uppercase;

      color: $color-default-white;
    }
  }

  &__total {
    margin: auto 0 0;
    padding: 21px 25px 23px;

    font-weight: 700;
    font-size: 20px;
    line-height: 20px;
    letter-spacing: 0.01em;
    text-transform: uppercase;

    border-radius: 11px;
    background-color: $color-pear;
    color: $color-night-rider;
  }

  &__table {
    border-collapse: collapse;
  }

  &__row {
    display: grid;
    grid-template-columns: repeat(7, minmax(100px, 152px));
    column-gap: 16px;
    row-gap: 16px;
    margin-bottom: 25px;
    padding-bottom: 24px;

    border-bottom: 1px dashed $color-very-light-grey;

    &:last-child {
      margin-bottom: 0;
      padding-bottom: 0;

      border-bottom: none;
    }

    &--head {
      padding-bottom: 0;

      border-bottom: none;
      border-radius: 8px;
      background-color: $color-night-rider;
    }
  }

  &__cell {
    padding: 0;

    &--head {
      padding: 21px 0;

      font-weight: 500;
      font-size: 18px;
      line-height: 18px;
      text-align: center;
      letter-spacing: 0.01em;
      text-transform: uppercase;

      color: $color-default-white;
    }
  }

  &__input {
    input {
      width: 100%;
      max-width: 152px;
      padding: 20px;

      font-weight: 400;
      font-size: 18px;
      line-height: 18px;
      text-align: center;

      border: 1px solid $color-very-light-grey;
      border-radius: 8px;
      color: $color-night-rider;

      outline: 0;

      transition: border $trans-default, box-shadow $trans-default;

      &:hover,
      &:active {
        border: 1px solid $color-night-rider;
      }

      &:focus {
        border: 1px solid $color-night-rider;
        box-shadow: 0 0 2px 2px $color-pear;
      }

      &:disabled {
        border: 1px solid $color-transparent;
        background-color: $color-white-smoke-5;
      }
    }

    &.is-invalid {
      border: 1px solid $color-venetian-red;

      &:hover,
      &:focus {
        border: 1px solid $color-venetian-red;
      }
    }
  }

  &__total-value {
    padding: 22px;

    border: 1px solid $color-suva-grey;
    border-radius: 8px;
    background-color: $color-night-rider;

    span {
      display: block;

      font-weight: 700;
      font-size: 20px;
      line-height: 20px;
      text-align: center;
      letter-spacing: 0.01em;
      text-transform: uppercase;

      color: $color-default-white;
    }
  }

  &__total-per-week {
    align-self: flex-end;
  }

  &__button {
    align-self: flex-end;

    max-width: 320px;
    margin-top: 25px;
  }
}
