.input-quantity {
  display: inline-flex;
  border-radius: 8px;
  border: 1px solid $color-very-light-grey;
  position: relative;
  background: $color-transparent;
  padding: 6px 21px;

  &__input {
    input {
      height: 40px;
      padding: 0;
      border: 0;
      margin: 0;
      background: $color-transparent;
      cursor: pointer;
      outline: 0;
      width: 72px;
      text-align: center;
      font-size: 20px;
      line-height: 20px;
      font-weight: 400;
      color: $color-night-rider;
    }
  }
}
