.btn-flat {
  position: relative;
  display: inline-flex;
  align-items: center;
  gap: 7px;
  padding: 0 0 1px;

  color: $color-night-rider;
  font-size: 14px;
  font-weight: 500;
  text-decoration: underline transparent;

  background-color: transparent;
  border: none;
  border-bottom: 2px solid transparent;
  cursor: pointer;

  transition: border $trans-default, background-color $trans-default, opacity $trans-default, text-decoration $trans-default, box-shadow $trans-default;

  &::before {
    position: absolute;
    content: "";
    top: -7px;
    left: -8px;

    width: calc(100% + 19px);
    height: calc(100% + 13px);
    border-radius: 80px;

    text-decoration: underline transparent;
    border: 1px solid #333333;
    box-shadow: 0 0 0 1px $color-pear;
    opacity: 0;

    transition: opacity $trans-default;
  }

  &--light {
    color: $color-default-white;
  }

  &:disabled,
  &.is-disabled {
    opacity: 0.3;
    pointer-events: none;
  }

  @include hover {
    border-bottom: 2px solid $color-pear;
    border-bottom-color: $color-pear;

    &::after {
      opacity: 1;
    }
  }

  @include focus {
    outline: none;

    &::before {
      opacity: 1;
    }
  }

  @include active {
    border-bottom: 2px solid $color-fuego;
    border-bottom-color: $color-fuego;

    &::before {
      opacity: 0;
    }
  }

  &--underlined {
    text-decoration: underline;
    /* stylelint-disable-next-line */
    -webkit-text-decoration: underline $color-night-rider;
    text-decoration-skip-ink: none;
    /* stylelint-disable-next-line */
    -webkit-text-decoration-skip: none;
    text-underline-offset: 2px;

    @include hover {
      text-decoration: underline $color-pear;
      /* stylelint-disable-next-line */
      -webkit-text-decoration: underline $color-pear;
      border-bottom: 2px solid transparent;
    }

    @include focus {
      text-decoration: underline transparent;
      /* stylelint-disable-next-line */
      -webkit-text-decoration: underline transparent;
    }

    @include active {
      text-decoration: underline $color-fuego;
      /* stylelint-disable-next-line */
      -webkit-text-decoration: underline $color-fuego;
      border-bottom: 2px solid transparent;
    }
  }

  svg {
    flex-shrink: 0;
  }

  span {
    text-align: left;
  }
}
