.btn-checkbox__btn {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  padding: 8px 21px;
  width: fit-content;

  color: $color-night-rider;
  font-size: 16px;
  font-weight: 400;
  line-height: 18px;
  user-select: none;

  background-color: transparent;
  border: 1px solid $color-very-light-grey;
  border-radius: 30px;
  cursor: pointer;

  transition: color $trans-default, background-color $trans-default, border $trans-default, box-shadow $trans-default;

  @include hover {
    border: 1px solid $color-night-rider;
  }

  input:checked + & {
    color: $color-pear;
    background-color: $color-night-rider;
    border: 1px solid $color-night-rider;

    @include hover {
      box-shadow: 0 0 0 2px $color-pear;
    }
  }
}

.btn-checkbox input {
  @include focus {
    & + .btn-checkbox__btn {
      border: 1px solid $color-night-rider;
      box-shadow: 0 0 0 2px $color-pear;
    }
  }
}
