.btn {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  row-gap: 20px;
  padding: 25px 20px;
  width: 100%;

  color: $color-nero;
  font-size: 16px;
  font-weight: 500;
  text-transform: uppercase;
  line-height: 16px;

  background-color: $color-pear;
  border: 2px solid transparent;
  border-radius: 80px;
  cursor: pointer;

  transition: color $trans-default, background-color $trans-default, border $trans-default, box-shadow $trans-default;

  &--medium {
    font-size: 12px;
    padding: 18px 15px;
  }

  &--small {
    font-size: 12px;
    padding: 12px 10px;
  }

  &:disabled,
  &.is-disabled {
    color: #a8a8a8;
    background-color: #f0f0f0;
    opacity: 1;

    pointer-events: none;
  }

  @include hover {
    background-color: $color-pear-2;
  }

  @include focus {
    border: 2px solid $color-night-rider;
    outline: none;
  }

  @include active {
    background-color: $color-fuego;
    border: 2px solid transparent;
    outline: none;
  }

  &--outlined {
    background-color: transparent;
    border: 1px solid $color-very-light-grey;

    @include hover {
      background-color: transparent;
      border: 1px solid $color-night-rider;
    }

    @include focus {
      background-color: transparent;
      border: 1px solid $color-night-rider;
      box-shadow: 0 0 0 2px $color-pear;
      outline: none;
    }

    @include active {
      border: 1px solid $color-night-rider;
      background-color: #f7f7f7;
      outline: none;
    }

    &:disabled,
    &.is-disabled {
      border: 1px solid transparent;
    }
  }

  &--dark-bg {
    &:disabled,
    &.is-disabled {
      color: #747474;
      background-color: rgba(247, 247, 247, 0.08);
      opacity: 1;

      pointer-events: none;
    }

    @include focus {
      border: 2px solid $color-nero;
      outline: none;
      box-shadow: 0 0 0 2px $color-pear;
    }

    @include active {
      border: 2px solid transparent;
      outline: none;
      box-shadow: none;
    }

    &.btn--outlined {
      color: $color-pear;
      background-color: transparent;
      border: 1px solid $color-pear;

      @include hover {
        color: $color-very-light-grey;
        border: 1px solid $color-very-light-grey;
      }

      @include focus {
        color: $color-very-light-grey;
        border: 1px solid #f2f2f2;
        box-shadow: 0 0 0 2px $color-pear;
      }

      @include active {
        color: $color-nero;
        border: 1px solid #333333;
        background-color: #f7f7f7;
      }

      &:disabled,
      &.is-disabled {
        color: #747474;
        background-color: rgba(247, 247, 247, 0.08);
        border: 1px solid transparent;
      }
    }
  }
}
