.slider-gyms {
  display: flex;

  &__list {
    position: relative;
    list-style: none;
    margin: 0;
    padding: 0;
    display: flex;
  }

  &__slide {
    display: none;
    border-radius: 0 12px 12px 0;
    overflow: hidden;

    &--current {
      display: flex;
    }
  }

  &__img {
    width: 100%;
    display: flex;

    & img {
      object-fit: cover;
      display: block;
      width: 100%;
      height: 100%;
    }
  }

  &__btn {
    position: absolute;
    bottom: 49%;

    &--prev {
      left: 30px;
    }

    &--next {
      right: 30px;
    }
  }
}
