.review {
  border: 1px solid $color-silver;
  border-radius: 20px;
  padding: 19px 14px;

  &__user-info {
    display: flex;
    align-items: center;
    margin-bottom: 12px;
  }

  &__user-photo {
    flex-shrink: 0;

    width: 64px;
    height: 64px;
    margin-right: 15px;

    border-radius: 50%;

    img {
      width: 64px;
      height: 64px;
      border-radius: 50%;

      object-fit: cover;
    }
  }

  &__user-name {
    display: block;
    margin-right: 15px;

    font-weight: 500;
    font-size: 18px;
    line-height: 18px;
    letter-spacing: 0.01em;
    text-transform: uppercase;

    color: $color-night-rider;
  }

  &__rating {
    display: flex;
    align-items: center;
    margin-left: auto;

    svg {
      width: 16px;
      height: 16px;
      margin-right: 6px;

      color: $color-pear;
    }

    span {
      font-weight: 400;
      font-size: 14px;
      line-height: 14px;

      color: $color-night-rider;
    }
  }

  &__comment {
    margin: 0;

    font-weight: 400;
    font-size: 16px;
    line-height: 19px;

    color: $color-charcoal;
  }
}
