.thumbnail-gym {
  position: relative;

  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 172px auto auto 1fr;
  row-gap: 20px;
  align-content: start;

  min-width: 334px;
  width: min-content;
  height: 100%;
  padding: 20px 18px 30px;

  background-color: $color-default-white;
  color: $color-night-rider;

  border: 1px solid $color-pear;
  box-shadow: 0 10px 30px rgba($color: $color-night-rider, $alpha: 0.1);
  border-radius: 20px;
  transition: box-shadow $trans-600-ease-in-out;

  @include hover {
    box-shadow: none;

    & .thumbnail-gym__image {
      filter: grayscale(0);
    }
  }
}

.thumbnail-gym__image {
  position: absolute;
  left: 1px;
  top: 1px;
  right: 1px;

  height: 190px;

  border: 1px solid $color-default-white;
  border-radius: 20px;

  overflow: hidden;
  z-index: 1;
  filter: grayscale(1);
  transition: filter $trans-400-ease-in-out;
}

.thumbnail-gym__image img {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.thumbnail-gym__verified {
  position: absolute;
  left: 20px;
  top: 21px;

  width: 32px;
  height: 32px;

  border-radius: 50%;

  background-color: $color-night-rider;
  color: $color-pear;

  z-index: 2;
}

.thumbnail-gym__verified svg {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate3d(-50%, -50%, 0);

  width: 14px;
  height: 14px;
}

.thumbnail-gym__favourite-button {
  position: absolute;
  right: 22px;
  top: 21px;

  padding: 0;
  margin: 0;
  width: 32px;
  height: 32px;

  border: 0;
  border-radius: 50%;

  background-color: $color-default-white;
  color: $color-bunker;

  z-index: 2;
  cursor: pointer;

  transition: box-shadow $trans-400-ease-in-out;

  @include hover {
    box-shadow: 0 3px 20px rgba($color: $color-night-rider, $alpha: 0.3);
  }

  @include focus {
    outline: 2px solid $color-pear;
    outline-offset: -2px;
  }

  @include active {
    box-shadow: none;
    outline: none;
    transition: none;
  }

  &.is-active {
    color: $color-torch-red;
  }

  &:disabled,
  &.is-disabled,
  &.is-disabled.focus-visible {
    background-color: $color-white-smoke-5;
    color: rgba($color: $color-night-rider, $alpha: 0.3);
    opacity: 1;

    cursor: default;
    pointer-events: none;
    outline: none;
  }
}

.thumbnail-gym__favourite-button svg {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate3d(-50%, -50%, 0);
}

.thumbnail-gym__header {
  grid-column: 1 / 2;
  grid-row: 2 / 3;

  display: grid;
  grid-template-columns: 1fr;
}

.thumbnail-gym__title {
  grid-column: 1 / 2;
  grid-row: 1 / 2;

  margin: 0;
  margin-bottom: 4px;

  font-size: 28px;
  font-weight: 500;
  line-height: 1.1;
  letter-spacing: 0.01em;
  text-transform: uppercase;
  color: $color-nero;
}

.thumbnail-gym__location {
  grid-column: 1 / 2;
  grid-row: 2 / 3;

  display: flex;
  align-items: center;

  font-size: 14px;
  line-height: 1;
  color: $color-dark-grey;
}

.thumbnail-gym__location svg {
  flex: 0 0 14px;
  width: 14px;
  height: 16px;
  align-self: flex-start;
}

.thumbnail-gym__location-address {
  margin-left: 8px;

  font-style: normal;
}

.thumbnail-gym__text-wrapper {
  grid-column: 1 / 2;
  grid-row: 3 / 4;
}

.thumbnail-gym__text {
  margin: 0;
  margin-bottom: 5px;

  font-size: 14px;
  line-height: 1.3;
}

.thumbnail-gym__buttons-wrapper {
  grid-column: 1 / 2;
  grid-row: 4 / 5;
  align-self: end;

  display: grid;
  row-gap: 6px;
  align-content: end;
}

.thumbnail-gym__button {
  padding: 16px;

  line-height: 1;
}
