.training-diary {
  &__wrapper {
    display: flex;
    flex-direction: column;
  }

  &__title {
    margin: 0;
    margin-bottom: 35px;

    font-weight: 700;
    font-size: 34px;
    line-height: 34px;
    letter-spacing: 0.01em;
    text-transform: uppercase;

    color: $color-nero;
  }

  &__block {
    display: grid;
    grid-template-columns: minmax(190px, 216px) 1fr;
    column-gap: 20px;
    margin-bottom: 35px;
  }

  &__sidebar {
    display: flex;
    flex-direction: column;

    border-radius: 12px;
    background-color: $color-night-rider;
  }

  &__icon {
    width: 17px;
    height: 18px;
    margin: 21px 0 26px 20px;

    color: $color-pear;
  }

  &__list {
    margin: 0;
    margin-bottom: 6px;
    padding: 0;

    list-style: none;
  }

  &__item {
    padding: 22px 0 0;

    span {
      display: block;
      margin-bottom: 10px;
      padding: 0 25px;

      font-weight: 500;
      font-size: 18px;
      line-height: 18px;
      letter-spacing: 0.01em;
      text-transform: uppercase;

      color: $color-pear;
    }
  }

  &__sublist {
    margin: 0;
    padding: 0;

    list-style: none;
  }

  &__subitem {
    padding: 24px 58px 23px;

    border-bottom: 1px dashed rgba($color-suva-grey, 0.3);

    span {
      display: block;
      margin: 0;
      padding: 0;

      font-weight: 400;
      font-size: 14px;
      line-height: 14px;
      letter-spacing: 0;
      text-transform: unset;

      color: $color-very-light-grey;
    }
  }

  &__total {
    padding-top: 26px;

    border-radius: 12px;
    background-color: $color-pear;
  }

  &__total-label {
    margin: 0;
    margin-bottom: 10px;
    padding: 0 25px;

    font-weight: 700;
    font-size: 20px;
    line-height: 20px;
    letter-spacing: 0.01em;
    text-transform: uppercase;

    color: $color-night-rider;
  }

  &__total-list {
    margin: 0;
    padding: 0;

    list-style: none;
  }

  &__total-item {
    padding: 24px 58px 23px;

    border-bottom: 1px dashed $color-suva-grey;

    &:last-child {
      border-bottom: none;
    }

    span {
      display: block;

      font-weight: 400;
      font-size: 14px;
      line-height: 14px;

      color: $color-night-rider;
    }
  }

  &__table {
    border-collapse: collapse;
  }

  &__row {
    display: grid;
    grid-template-columns: repeat(7, minmax(96px, 152px));
    column-gap: 16px;
    row-gap: 16px;
    margin-bottom: 10px;

    &:nth-child(2n-1) {
      margin-bottom: 60px;
    }

    &:nth-last-child(3) {
      margin-bottom: 72px;
    }

    &:last-child {
      margin-bottom: 0;
    }

    &--head {
      border-radius: 8px;
      background-color: $color-night-rider;
    }
  }

  &__cell {
    padding: 0;

    &--head {
      padding: 21px 0;

      font-weight: 500;
      letter-spacing: 0.01em;
      text-transform: uppercase;

      color: $color-default-white;
    }
  }

  &__data {
    padding: 16px;

    border: 1px solid $color-very-light-grey;
    border-radius: 8px;
    background-color: rgba($color-dark-gray-2, 0.1);

    span {
      display: block;

      font-weight: 400;
      font-size: 18px;
      line-height: 18px;
      text-align: center;

      color: $color-night-rider;
    }

    &--total {
      padding: 15px 16px;

      border: 1px solid $color-suva-grey;
      background-color: $color-night-rider;

      span {
        font-weight: 700;
        font-size: 20px;
        line-height: 20px;
        letter-spacing: 0.01em;
        text-transform: uppercase;

        color: $color-default-white;
      }
    }
  }

  &__total-per-week {
    align-self: flex-end;

    .total__title-wrapper {
      margin-bottom: 20px;
    }
  }
}
