.btn-radio-sort {
  cursor: pointer;
  display: flex;
  flex-direction: row;
  list-style: none;

  input {
    position: absolute;

    width: 1px;
    height: 1px;
    overflow: hidden;

    border: 0;

    clip: rect(0, 0, 0, 0);

    @include focus {
      & + span {
        box-shadow: 0 0 1px 1px $color-pear;
      }
    }
  }

  label {
    position: relative;
    cursor: pointer;

    width: 156px;
    height: 40px;
    margin-right: -1px;

    text-align: center;
    color: $color-night-rider;

    background-color: $color-white-smoke-3;
    border: none;
    outline: none;

    transition: 0.3s;

    @include hover {
      background: $color-white-smoke-2;
    }
  }

  span {
    position: absolute;
    top: 0;
    left: 0;

    width: 100%;
    height: inherit;
    padding-top: 10px;

    font-weight: 400;
    font-size: 14px;
    line-height: 14px;
    text-align: center;
  }

  input:checked + span {
    background-color: $color-night-rider;
    box-shadow: 0 -7px 0 -5px $color-pear inset;
    color: $color-pear;
    font-weight: 500;
  }

  label:first-of-type {
    border-radius: 8px 0 0 8px;

    span {
      border-radius: 8px 0 0 8px;
    }
  }

  label:last-of-type {
    border-radius: 0 8px 8px 0;

    span {
      border-radius: 0 8px 8px 0;
    }
  }
}
