.certificate-card {
  display: flex;
  flex-direction: column;
  width: 334px;
  padding: 29px 19px;

  border: 1px solid $color-very-light-grey;
  border-radius: 20px;

  &--edit {
    .certificate-card__button {
      &--edit {
        display: none;
      }

      &--save {
        display: flex;
      }
    }

    .certificate-card__controls {
      display: flex;
    }
  }

  &__image {
    width: 294px;
    height: 360px;
    margin-bottom: 16px;

    img {
      width: 294px;
      height: 360px;
    }
  }

  &__buttons {
    display: flex;
    justify-content: space-between;
    align-items: center;
    min-height: 36px;
  }

  &__button {
    margin-right: 20px;

    &--save {
      display: none;
    }
  }

  &__controls {
    display: none;
  }

  &__control {
    width: 36px;
    height: 36px;
    margin-right: 12px;

    &:last-child {
      margin-right: 0;
    }
  }
}
