.role-btn {
  position: relative;
  width: 334px;
}

.role-btn__icon {
  position: absolute;
  top: 17px;
  left: 20px;

  display: inline-flex;
  justify-content: center;
  align-items: center;
  width: 26px;
  height: 26px;

  color: $color-white-smoke-2;

  background-color: $color-night-rider;
  border-radius: 50%;
  cursor: pointer;

  transition: color $trans-default;

  input:checked ~ & {
    color: $color-pear;
  }

  input:disabled ~ &,
  .is-disabled & {
    background-color: rgba($color-night-rider, 0.15);
  }
}

.role-btn__btn {
  display: inline-flex;
  justify-content: flex-start;
  align-items: center;
  padding: 20px 60px;
  width: 100%;

  color: $color-night-rider;
  font-size: 16px;
  font-weight: 400;

  background-color: transparent;
  border: 1px solid $color-very-light-grey;
  border-radius: 8px;
  cursor: pointer;

  transition: background-color $trans-default, border $trans-default, box-shadow $trans-default, font-weight $trans-default;

  @include hover {
    border: 1px solid $color-pear;
    background-color: $color-default-white;
    box-shadow: none;
  }

  input:checked ~ & {
    font-weight: 500;
    background-color: $color-default-white;
    border: 1px solid $color-pear;
  }

  input:disabled ~ &,
  .is-disabled & {
    color: $color-dark-gray-3;
    border: 1px solid #f0f0f0;
    background-color: #f0f0f0;
    pointer-events: none;
  }
}

.role-btn input {
  @include focus {
    & ~ .role-btn__btn {
      outline: none;
      border: 1px solid $color-night-rider;
      box-shadow: 0 0 0 2px $color-pear;
    }
  }

  @include active {
    & ~ .role-btn__btn {
      font-weight: 500;
      outline: none;
      border: 1px solid $color-pear;
      box-shadow: none;
    }
  }
}
