.my-training-form {
  &__wrapper {
    border-radius: 0 20px 20px 0;
    background-color: $color-default-white;
    border: 1px solid $color-pear;
    max-width: 356px;
    padding: 42px 22px 60px 22px;
    box-shadow: 0 10px 30px rgba($color: $color-night-rider, $alpha: 0.33);
  }

  &__btnback {
    margin-bottom: 53px;
    padding-left: 0;

    font-weight: 500;
    font-size: 14px;
    line-height: 14px;

    word-break: break-word;
  }


  &__title {
    text-transform: uppercase;
    font-weight: 500;
    font-size: 18px;
    line-height: 18px;
    margin: 0 0 35px 0;
    color: $color-nero;
    letter-spacing: 0.01em;
  }

  &__form {
    display: flex;
    flex-direction: column;
  }

  &__check-list {
    margin: 0;
    padding: 0;
    list-style: none;
  }

  &__block {
    margin-bottom: 30px;
  }

  &__check-list-item {
    margin-bottom: 20px;
  }

  &__block:last-of-type,
  &__check-list-item:last-child {
    margin-bottom: 0;
  }

  &__block-title {
    margin: 0 0 15px 0;

    font-weight: 500;
    font-size: 14px;
    line-height: 14px;
    color: $color-night-rider;
    text-transform: uppercase;
    letter-spacing: 0.02em;
  }

  &__block--price {
    margin-bottom: 36px;
  }

  &__block--calories {
    margin-bottom: 37px;

    .my-training-form__block-title {
      margin-bottom: 18px;
    }
  }

  &__block--raiting {
    margin-bottom: 30px;

    .my-training-form__block-title {
      margin-bottom: 22px;
    }
  }

  &__block--duration {
    .my-training-form__block-title {
      margin-bottom: 22px;
    }
  }

  .filter-price {
    margin-bottom: 22px;
  }

  .filter-calories {
    margin-bottom: 23px;
  }

  .custom-toggle {
    padding: 0;
  }
}
