.questionnaire-coach {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;

  &__wrapper {
    display: flex;
    flex-direction: column;
    width: 100%;
    margin-bottom: 25px;
  }

  &__block {
    padding-bottom: 30px;
    margin-bottom: 30px;

    border-bottom: 1px dashed $color-very-light-grey;

    &:last-child {
      margin-bottom: 0;
      padding-bottom: 0;

      border-bottom: none;
    }
  }

  &__legend {
    display: block;

    font-weight: 500;
    font-size: 20px;
    line-height: 20px;
    letter-spacing: 0.01em;
    text-transform: uppercase;

    color: $color-nero;
  }

  &__specializations {
    max-width: 540px;
    margin-top: 25px;
  }

  &__radio {
    flex-direction: row;
    column-gap: 20px;
    margin-top: 25px;
    padding: 0;

    .custom-toggle-radio__block {
      margin-bottom: 0;
    }
  }

  &__drag-and-drop {
    margin-top: 20px;
  }

  &__textarea {
    margin-top: 20px;
    margin-bottom: 25px;
  }

  &__checkbox {
    align-self: flex-start;

    position: relative;
    display: flex;


    input {
      position: absolute;

      width: 1px;
      height: 1px;
      margin: -1px;

      clip: rect(0 0 0 0);
    }

    input:checked ~ .questionnaire-coach__checkbox-label {
      font-weight: 500;
    }

    input:checked + .questionnaire-coach__checkbox-icon {
      &::before {
        background-color: $color-night-rider;

        width: 24px;
        height: 24px;
        border-radius: 2px;
      }

      svg {
        display: block;
        position: absolute;
        content: "";
        top: 50%;
        left: 50%;

        color: $color-pear;

        transform: translate(-50%, -50%);

        transition: background-color 0.3s ease;
      }
    }

    input:focus-visible + .questionnaire-coach__checkbox-icon {
      box-shadow: 0 0 1px 1px $color-pear;
    }

    input:active + .questionnaire-coach__checkbox-icon {
      box-shadow: 0 0 1px 1px $color-pear;
    }

    label {
      position: relative;

      padding: 2px 0 3px 32px;

      cursor: pointer;

      span {
        margin-bottom: 0;
        margin-left: 0;
      }
    }
  }

  &__checkbox-icon {
    position: absolute;
    top: 0;
    left: 0;

    width: 24px;
    height: 24px;

    border: 1px solid $color-night-rider;
    border-radius: 2px;

    transition: $trans-default;

    svg {
      display: none;
    }

    &::before {
      content: "";
      position: absolute;
      top: 50%;
      left: 50%;

      width: 24px;
      height: 24px;

      background-color: $color-transparent;

      transform: translate(-50%, -50%);

      transition: $trans-default;
    }

    &:hover {
      background-color: $color-white-smoke-2;
    }

    &:active {
      box-shadow: 0 0 1px 1px $color-pear;
    }
  }

  &__checkbox-label {
    font-weight: 400;
    font-size: 16px;
    line-height: 16px;

    color: $color-night-rider;

    span {
      text-decoration: underline;
      text-underline-offset: 2px;
    }
  }

  &__button {
    max-width: 256px;
    margin-top: 20px;
  }
}
