.notification {
  position: relative;
  display: block;
  padding: 15px 10px 15px 28px;

  border-radius: 8px;

  cursor: pointer;

  outline: 0;

  transition: background-color $trans-default;

  &.is-active {
    &::before {
      content: "";
      position: absolute;
      top: 19px;
      left: 10px;
      width: 8px;
      height: 8px;

      border-radius: 50%;
      background-color: $color-pear;
    }
  }

  @include hover-focus {
    background-color: $color-white-smoke-3;

    .notification__text {
      font-weight: 500;
    }
  }

  &__text {
    margin: 0;
    margin-bottom: 4px;

    font-weight: 400;
    font-size: 14px;
    line-height: 17px;

    color: $color-night-rider;

    transition: font-weight $trans-default;
  }

  &__time {
    display: block;

    font-weight: 400;
    font-size: 12px;
    line-height: 12px;

    color: $color-dark-gray;
  }
}
