.input-load-avatar {
  width: 98px;
  height: 98px;
}

.input-load-avatar__avatar {
  display: block;
  border-radius: 50%;
  cursor: pointer;

  transition: box-shadow $trans-default;
}

.input-load-avatar__btn {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;

  width: 100%;
  height: 100%;
  border: 1px solid $color-night-rider;
  border-radius: 50%;

  transition: box-shadow $trans-default;
}

.input-load-avatar input {
  @include hover {
    & + .input-load-avatar__btn,
    & + .input-load-avatar__avatar {
      box-shadow: 0 3px 20px rgba(51, 51, 51, 0.1);
      cursor: pointer;
    }
  }

  @include focus {
    & + .input-load-avatar__btn,
    & + .input-load-avatar__avatar {
      box-shadow: 0 0 0 2px $color-pear;
      outline: none;
    }
  }

  @include active {
    & + .input-load-avatar__btn,
    & + .input-load-avatar__avatar {
      box-shadow: none;
      outline: none;
    }
  }
}
