.search {
  position: relative;

  border-radius: 8px;
  background-color: $color-white-smoke-2;

  &.is-active {
    .search__list {
      display: block;
    }
  }

  .search__label {
    position: absolute;

    width: 1px;
    height: 1px;
    margin: -1px;

    clip: rect(0 0 0 0);
  }

  input {
    width: 236px;
    margin: 0;
    padding: 16px 50px 16px 16px;

    border: 1px solid $color-very-light-grey;
    border-radius: 8px;
    background-color: $color-transparent;

    outline: 0;

    transition: border $trans-default, box-shadow $trans-default;

    &:hover,
    &:active {
      border: 1px solid $color-night-rider;
    }

    &:focus {
      border: 1px solid $color-night-rider;
      box-shadow: 0 0 2px 2px $color-pear;
    }

    &:disabled {
      border: 1px solid $color-nero;
      background-color: $color-dark-gray-2;
      opacity: 0.08;

      + .search__icon {
        opacity: 0.2;
      }
    }
  }

  &__icon {
    position: absolute;
    top: 16px;
    right: 20px;
    width: 20px;
    height: 20px;

    color: $color-night-rider;
  }

  &__list {
    position: absolute;
    display: none;
    width: 236px;
    max-height: 206px;
    margin: 0;
    margin-top: 22px;
    padding: 0 3px;

    border: 1px solid $color-very-light-grey;
    border-radius: 8px;
    background-color: $color-snow;

    list-style: none;

    overflow: auto;
  }

  &__link {
    display: block;
    padding: 13px 12px 9px;

    font-weight: 400;
    font-size: 16px;
    line-height: 16px;

    border-radius: 8px;
    color: $color-night-rider;

    &.is-active {
      background-color: $color-pear;
    }
  }
}
