.gym-card {
  &__wrapper {
    display: grid;

    background-color: $color-night-rider;
    border-radius: 12px;

    grid-template-columns: 1fr 59%;
  }

  &__content {
    padding: 88px 0 64px;
  }

  &__head {
    display: grid;
    grid-template-columns: 1fr min-content;
    margin: 0 0 12px 0;
    padding: 0 99px;
  }

  &__title {
    margin: 0 10px 0 0;

    font-weight: 700;
    font-size: 34px;
    line-height: 34px;
    color: $color-default-white;
    text-transform: uppercase;
    letter-spacing: 0.01em;
  }

  &__icon {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 32px;
    height: 32px;

    background-color: $color-pear;
    border-radius: 100px;
  }

  &__verify-bold {
    color: $color-night-rider;
  }

  &__address {
    display: grid;
    grid-template-columns: min-content 1fr;
    align-items: center;
    margin: 0 0 29px 0;
    padding: 0 99px;
    font-weight: 400;
    font-size: 14px;
    line-height: 14px;
    color: $color-dark-gray;

    span {
      padding: 0 0 0 8px;
    }
  }

  &__hashtag-list {
    display: flex;
    flex-wrap: wrap;
    margin: 0 0 28px 0;
    padding: 0 99px;

    list-style: none;
  }

  &__hashtag-item {
    margin: 0 8px 5px 0;
  }

  &__hashtag-item:last-child {
    margin: 0;
  }

  &__text {
    margin: 0 0 35px 0;
    padding: 35px 99px 0;
    border-top: 1px solid rgba($color-nobel, 0.1);

    p {
      max-width: 354px;
      margin: 0 0 15px 0;

      font-weight: 400;
      font-size: 16px;
      line-height: 22px;
      color: $color-default-white;
    }

    p:last-of-type {
      margin: 0;
    }
  }

  &__rating-price {
    display: flex;
    flex-wrap: wrap;
    justify-content: start;
    margin: 0 0 35px;
    padding: 0 99px;
  }

  &__rating {
    padding: 29px 32px;
    margin: 0 18px 15px 0;
    border: 1px solid rgba($color-nobel, 0.1);
    border-radius: 8px;
  }

  &__button {
    padding: 0 99px;
    margin: 0 0 10px;
  }
}
