.special-offers {
  &__wrapper {
    display: flex;
    padding-bottom: 70px;
  }

  &__list {
    display: flex;
    max-width: 1040px;
    margin: 0;
    margin-right: 21px;
    padding: 0;

    list-style: none;
  }

  &__item {
    display: none;

    &.is-active {
      display: block;
    }
  }
}
