.my-orders {
  &__wrapper {
    display: flex;
    flex-direction: column;
    padding-top: 18px;
    padding-bottom: 60px;
  }

  &__back {
    margin-bottom: 40px;
  }

  &__title-wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 35px;
  }

  &__title {
    margin: 0;
    margin-right: 20px;

    font-weight: 700;
    font-size: 34px;
    line-height: 34px;
    letter-spacing: 0.01em;
    text-transform: uppercase;

    color: $color-nero;
  }

  &__sort {
    display: flex;
    align-items: center;
    column-gap: 20px;
  }

  &__label {
    display: block;

    font-weight: 400;
    font-size: 16px;
    line-height: 16px;

    color: $color-suva-grey;
  }

  &__sort-button {
    font-weight: 400;
    font-size: 16px;
    line-height: 16px;

    color: $color-suva-grey;

    &.is-active {
      color: $color-night-rider;

      cursor: default;
      pointer-events: none;
    }
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    column-gap: 20px;
    row-gap: 20px;
    margin: 0;
    margin-bottom: 60px;
    padding: 0;

    list-style: none;
  }

  &__item {
    width: 100%;
  }

  &__show-more {
    align-self: center;
  }
}
