.gym-catalog-form {
  &__wrapper {
    max-width: 356px;
    padding: 48px 22px 64px 22px;

    background-color: $color-default-white;
    border: 2px solid $color-pear;
    border-radius: 0 20px 20px 0;
    box-shadow: 0 10px 30px rgba($color: $color-night-rider, $alpha: 0.33);
  }

  &__btnback {
    margin-bottom: 46px;
    padding-left: 0;

    font-weight: 500;
    font-size: 14px;
    line-height: 14px;
    word-break: break-word;
  }


  &__title {
    text-transform: uppercase;
    font-weight: 500;
    font-size: 18px;
    line-height: 18px;
    margin: 0 0 35px 0;
    color: $color-nero;
  }

  &__title--sort {
    margin-bottom: 30px;
  }

  &__form {
    display: flex;
    flex-direction: column;
  }

  &__check-list {
    margin: 0;
    padding: 0;

    list-style: none;
  }

  &__block {
    margin-bottom: 30px;
  }

  &__block--price {
    margin-bottom: 40px;

    .filter-price {
      margin-bottom: 22px;
    }
  }

  &__block--calories {
    margin-bottom: 39px;

    .filter-calories {
      margin-bottom: 21px;
    }
  }

  &__block--rating {
    margin-bottom: 28px;

    .gym-catalog-form__block-title {
      margin-bottom: 22px;
    }
  }

  &__block--type {
    margin-bottom: 40px;

    .gym-catalog-form__block-title {
      margin-bottom: 25px;
    }

    .gym-catalog-form__check-list {
      display: grid;

      grid-template-columns: 148px 150px;
      row-gap: 20px;
    }
  }

  &__block:last-of-type {
    margin-bottom: 0;
  }

  &__block-title {
    margin: 0 0 15px 0;

    font-weight: 500;
    font-size: 14px;
    line-height: 14px;
    color: $color-night-rider;
    text-transform: uppercase;
    letter-spacing: 0.02em;
  }

  .btn-radio-sort__label {
    padding-top: 14px;
    padding-bottom: 16px;
  }

  .btn-radio-sort label {
    width: 90px;
    height: 45px;

    span {
      padding-top: 14px;
    }
  }

  .btn-radio-sort label:first-child {
    width: 103px;
  }

  .btn-radio-sort label:last-child {
    width: 120px;
  }

  .custom-toggle {
    padding: 0;
  }
}
