.gym-hall-form {
  &__wrapper {
    max-width: 356px;
    padding: 42px 22px 34px 22px;

    background-color: $color-default-white;
    border: 2px solid $color-pear;
    border-radius: 0 20px 20px 0;
    box-shadow: 0 10px 30px rgba($color: $color-night-rider, $alpha: 0.33);
  }

  &__btnback {
    margin-bottom: 50px;
    padding-left: 0;

    font-weight: 500;
    font-size: 14px;
    line-height: 14px;
    word-break: break-word;
  }


  &__title {
    text-transform: uppercase;
    font-weight: 500;
    font-size: 18px;
    line-height: 18px;
    margin: 0 0 35px 0;
    color: $color-nero;
    letter-spacing: 0.01em;
  }

  &__title--status {
    margin-bottom: 20px;
  }

  &_form {
    display: flex;
    flex-direction: column;
  }

  &__check-list {
    margin: 0;
    padding: 0;

    list-style: none;
  }

  &__check-list-item {
    margin-bottom: 12px;
  }

  &__block {
    margin-bottom: 30px;
  }

  &__block:last-of-type,
  &__check-list-item:last-child {
    margin-bottom: 0;
  }

  &__block:first-child {
    margin-bottom: 40px;
  }

  &__block-title {
    margin: 0 0 25px 0;

    font-weight: 500;
    font-size: 14px;
    line-height: 14px;
    color: $color-night-rider;
    text-transform: uppercase;
    letter-spacing: 0.02em;
  }

  &__block--location {
    margin-bottom: 30px;

    .gym-hall-form__check-list-item {
      margin-bottom: 15px;
    }
  }

  &__block-title--price {
    margin-bottom: 15px;
  }

  &__block--addition {
    margin-bottom: 30px;

    .gym-hall-form__block-title {
      margin-bottom: 22px;
    }

    .gym-hall-form__check-list-item {
      margin-bottom: 20px;
    }

    .gym-hall-form__check-list-item:last-child {
      margin-bottom: 0;
    }
  }

  &__btn-show {
    margin-top: 14px;
    padding-left: 0;
  }

  .filter-price {
    margin-bottom: 22px;
  }

  .custom-toggle {
    padding: 0;
  }
}
