.popup {
  // position: fixed;
  display: flex;
  width: 100%;
  min-height: 100%;
  justify-content: center;
  align-items: center;
  background: rgba($color-nero, 0.9);
  z-index: 100;
  flex-shrink: 0;
  padding: 40px 0 30px;

  &__wrapper {
    box-shadow: 0 10px 30px rgba($color-night-rider, 0.1);
    border: 1px solid $color-pear;
    border-radius: 60px;
    overflow: hidden;
    z-index: 2;
    width: 690px;

    &--map {
      width: 1160px;
    }
  }

  &__content {
    padding: 30px 118px;
    box-shadow: inset 0 10px 30px rgba($color-night-rider, 0.1);
    background-color: $color-default-white;

    &--membership {
      padding: 39px 118px 60px;
    }

    &--purchases {
      padding: 35px 118px 60px;
    }

    &--certificates {
      position: relative;
      padding: 17px 177px;
    }

    &--feedback {
      padding: 35px 118px 60px;
    }
  }

  &__product {
    display: grid;
    grid-template-columns: 118px 1fr 1fr;
    margin: 0 0 35px;
  }

  &__product-image {
    margin: 0 20px 0 0;
  }

  &__product-image img {
    border-radius: 12px;
    width: 98px;
    height: 80px;
    display: block;
  }

  &__product-info {
    margin: 15px 0 0;
  }

  &__product-title {
    font-weight: 500;
    font-size: 24px;
    line-height: 24px;
    letter-spacing: 0.01em;
    text-transform: uppercase;
    margin: 0 0 7px;
  }

  &__product-price {
    font-weight: 400;
    font-size: 18px;
    line-height: 20px;
    margin: 0;
  }

  &__product-quantity {
    justify-self: end;
    margin: 0 0 0 auto;
  }

  &__quantity {
    margin: 0 0 10px 0;
    color: $color-dark-gray-2;
    font-size: 16px;
    line-height: 16px;
    font-weight: 400;
  }

  &__total {
    margin: 0 0 45px;
    display: grid;
    grid-template-columns: min-content 1fr min-content;
    align-items: end;

    & span {
      border-bottom: 1px dashed $color-very-light-grey;
    }
  }

  &__total-text {
    font-size: 18px;
    line-height: 18px;
    font-weight: 500;
    letter-spacing: 0.01em;
    margin: 0;
  }

  &__total-dash {
    width: 100%;
    height: 4px;
  }

  &__total-price {
    font-size: 28px;
    line-height: 28px;
    font-weight: 500;
    letter-spacing: 0.01em;
    margin: 0;
    overflow-wrap: break-word;
  }

  &__button {
    max-width: 256px;
    margin: 0 auto 10px;
  }

  &__map {
    position: relative;
  }

  &__pin {
    width: 99px;
    height: 71px;
    position: absolute;
    left: 50%;
    transform: translateX(96px);
    top: 333px;

    &--user {
      width: 40px;
      height: 49px;
      transform: translateX(124px);
      top: 337px;
    }
  }

  &__pin-icon {
    color: $color-pear;
  }

  &__slider-buttons {
    display: flex;
    position: absolute;
    right: 19px;
  }

  &__slider-btn--prev {
    margin: 0 15px 0 0;
  }

  &__slider-list {
    list-style: none;
    padding: 0;
    margin: 0;
  }

  &__slide {
    display: none;
    border-radius: 0 12px 12px 0;
    overflow: hidden;

    &--current {
      display: flex;
    }
  }

  &__slide-img {
    padding: 30px 20px 82px;
    border: 1px solid $color-very-light-grey;
    border-radius: 20px;
  }

  &__feedback {
    margin: 0 0 45px;
    width: 452px;
  }

  &__rate-list {
    list-style: none;
    padding: 0;
    margin: 10px 0 30px;
    display: grid;
    grid-template-columns: repeat(5, 40px);
    gap: 10px;
  }

  &__rate-item {
    input {
      position: absolute;
      width: 1px;
      height: 1px;
      overflow: hidden;
      border: 0;
      clip: rect(0, 0, 0, 0);

      @include hover {
        + .popup__rate-number {
          border: 1px solid $color-pear;
          background-color: $color-default-white;
        }
      }

      @include focus {
        + .popup__rate-number {
          border: 1px solid $color-nero;
          box-shadow: 0 0 2px 2px $color-pear;
          background-color: $color-default-white;
          color: $color-night-rider;
          outline: none;
        }
      }

      @include active {
        + .popup__rate-number {
          background-color: $color-night-rider;
          color: $color-pear;
          border: 1px solid $color-pear;
          outline: none;
          box-shadow: none;
        }
      }

      &:checked + .popup__rate-number {
        background-color: $color-night-rider;
        color: $color-pear;
        border: 1px solid $color-pear;
        outline: none;
      }

      &:checked {
        @include hover {
          +.popup__rate-number {
            background-color: $color-night-rider;
            color: $color-pear;
            border: 1px solid $color-pear;
            outline: none;
          }
        }
      }

      &:disabled + .popup__rate-number {
        color: $color-dark-gray-3;
        background-color: $color-white-smoke-5;
        border: 1px solid $color-transparent;
        pointer-events: none;
      }
    }
  }

  &__rate-item-wrap {
    &.is-disabled {
      pointer-events: none;
      opacity: 1;

      span {
        color: $color-dark-gray-3;
        background-color: $color-white-smoke-5;
        border: 1px solid $color-transparent;
        opacity: 1;
      }
    }
  }

  &__rate-number {
    width: 40px;
    height: 40px;
    border: 1px solid $color-very-light-grey;
    border-radius: 8px;
    background-color: $color-default-white;
    display: block;
    color: $color-night-rider;
    padding: 9px 8px 7px;
    text-align: center;
    transition: border $trans-default, background-color $trans-default, color $trans-default, box-shadow $trans-default;
    font-weight: 500;
    cursor: pointer;
  }

  &__feedback-title {
    margin: 0 0 20px;
    font-size: 16px;
    line-height: 16px;
    font-weight: 400;

    &--text {
      margin: 0 0 15px;
    }
  }

  &__feedback-textarea {
    .custom-textarea label textarea {
      min-height: 160px;
    }
  }
}
