.btn-radio-image {
  cursor: pointer;

  &.is-disabled {
    opacity: 1;
  }

  &.is-disabled + .btn-radio-image__image {
    border: 1px solid $color-transparent;
    background-color: $color-white-smoke-5;
    pointer-events: none;
  }

  &.is-disabled + .btn-radio-image__image > svg,
  &.is-disabled + .btn-radio-image__image > img {
    opacity: 0.2;
    pointer-events: none;
  }

  input:disabled + .btn-radio-image__image {
    border: 1px solid $color-transparent;
    background-color: $color-white-smoke-5;
    pointer-events: none;
  }

  input:disabled + .btn-radio-image__image > svg,
  input:disabled + .btn-radio-image__image > img {
    opacity: 0.2;
    pointer-events: none;
  }

  & label {
    position: relative;
    cursor: pointer;
  }

  &__image {
    border-radius: 8px;
    padding: 28px 0 27px;
    border: 1px solid $color-very-light-grey;
    background-color: $color-white-smoke-3;
    width: 144px;
    height: 80px;
    transition: border $trans-default, background-color $trans-default, box-shadow $trans-default;
    display: flex;

    & img {
      margin: auto;
      display: flex;
      transition: opacity $trans-default;
    }

    & svg {
      margin: auto;
      display: flex;
      transition: opacity $trans-default;
    }
  }

  input {
    position: absolute;
    width: 1px;
    height: 1px;
    overflow: hidden;
    border: 0;
    clip: rect(0, 0, 0, 0);

    @include focus {
      + .btn-radio-image__image {
        border: 1px solid $color-nero;
        box-shadow: 0 0 2px 2px $color-pear;
        background-color: $color-default-white;
        outline: none;
      }
    }

    @include hover {
      + .btn-radio-image__image {
        border: 1px solid $color-night-rider;
        background-color: $color-default-white;
      }
    }

    @include active {
      + .btn-radio-image__image {
        border: 1px solid $color-pear;
        background-color: $color-default-white;
        box-shadow: none;
        outline: none;
      }
    }
  }

  input:checked + .btn-radio-image__image {
    border: 1px solid $color-pear;
    background-color: $color-default-white;
    box-shadow: none;
    outline: none;
  }
}
