.custom-input,
.custom-textarea {
  position: relative;
  display: flex;
  width: 100%;

  label {
    display: flex;
    flex-direction: column;
    width: 100%;

    input,
    textarea {
      width: 100%;
      min-height: 50px;
      padding: 14px 20px;

      border: 1px solid rgba($color-nero, 0.1);
      border-radius: 8px;
      outline: none;

      transition: border $trans-default, box-shadow $trans-default, color $trans-default, background-color $trans-default;
      background-color: rgba($color-dark-gray-2, 0.1);

      &:placeholder-shown {
        background-color: transparent;
        border: 1px solid $color-very-light-grey;
      }

      &:hover,
      &:active {
        outline: none;
        background-color: transparent;
        border: 1px solid $color-night-rider;
        box-shadow: none;
      }

      &:focus {
        outline: none;
        background-color: transparent;
        border: 1px solid $color-night-rider;
        box-shadow: 0 0 0 2px $color-pear;
      }

      &:disabled,
      .is-disabled & {
        border: 1px solid $color-nero;
        background-color: $color-dark-gray-2;
        opacity: 0.08;
      }
    }

    textarea {
      min-height: 120px;
      padding: 15px 20px;
      font-size: 99%;
      line-height: 1.2;
    }
  }

  &--readonly {
    label input:disabled,
    label textarea:disabled {
      color: $color-night-rider;
      -webkit-text-fill-color: $color-night-rider;
      border: none;
      background-color: transparent;
      opacity: 1;
      -webkit-opacity: 1;
      pointer-events: none;
    }
  }

  &--with-text-right label input {
    padding: 14px 60px 14px 20px;
  }

  &--with-text-left label input {
    padding: 14px 20px 14px 60px;
  }

  &--error label input {
    border: 1px solid $color-venetian-red;
    background-color: transparent;
  }

  &__label {
    color: $color-dark-gray-2;
    margin-bottom: 9px;

    .is-disabled & {
      color: rgba($color-dark-gray-2, 0.5);
    }
  }

  &__error {
    margin-top: 6px;
    color: $color-venetian-red;
    opacity: 0;
    transition: opacity $trans-default;

    .custom-input--error & {
      opacity: 1;
    }
  }

  &__wrapper {
    position: relative;
    width: 100%;
    display: block;
  }

  &__text {
    position: absolute;
    top: 15px;
    color: $color-dark-gray-2;
    transition: color $trans-default;

    .custom-input--with-text-right & {
      right: 20px;
    }

    .custom-input--with-text-left & {
      left: 19px;
    }

    input:hover + &,
    input:focus + &,
    input:active + & {
      color: $color-night-rider;
    }
  }
}
