.intro {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;

  display: flex;
  flex-direction: column;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;


  &__background {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__wrapper {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    flex-shrink: 0;
    width: 100%;
    min-height: 100%;
    padding: 211px 22px 22px;
  }

  &__icon {
    position: absolute;
    top: 22px;
    right: 22px;
    width: 60px;
    height: 60px;
  }

  &__title-logo {
    position: relative;
    width: 934px;
    height: 455px;
    margin: 0 auto 81px;
  }

  &__buttons {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    row-gap: 20px;
    width: 100%;
    margin-top: 20px;
    padding-right: 65px;
  }

  &__button {
    max-width: fit-content;
    min-width: 216px;
    margin-right: 40px;
  }

  &__text {
    margin: 0;

    font-weight: 300;
    font-size: 16px;
    line-height: 16px;
    letter-spacing: 0.01em;
    text-transform: uppercase;

    color: $color-default-white;
  }

  &__link {
    text-decoration: underline;
    text-decoration-skip-ink: none;
    text-underline-offset: 2px;

    color: $color-pear;

    transition: color $trans-default;

    @include hover-focus {
      color: $color-pear-2;
    }
  }
}
