.gyms-catalog {
  display: flex;
  flex-direction: column;

  &__list {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    column-gap: 20px;
    row-gap: 20px;
    margin: 0;
    margin-bottom: 60px;
    padding: 0;

    list-style: none;
  }

  &__item {
    width: 100%;
  }

  &__show-more {
    align-self: center;
  }
}
