.hashtag {
  display: inline-flex;
  flex-direction: row;
  align-items: center;
  padding: 10px 12px;

  font-weight: 400;
  font-size: 12px;
  line-height: 12px;
  color: $color-night-rider;

  background-color: $color-transparent;
  border: 1px solid $color-nobel;
  border-radius: 100px;

  &--white {
    color: $color-default-white;
  }

  &--gym-card {
    color: $color-default-white;
  }
}
