.reviews-side-bar {
  display: flex;
  flex-direction: column;
  width: 356px;
  height: 100%;
  padding: 44px 21px;

  border: 1px solid $color-pear;
  border-radius: 0 20px 20px 0;
  background-color: $color-default-white;
  box-shadow: 0 10px 150px rgba($color-default-black, 0.15);

  &__back {
    display: flex;
    align-items: center;
    margin-bottom: 55px;
  }

  &__title {
    margin: 0;
    margin-bottom: 30px;

    font-weight: 500;
    font-size: 18px;
    line-height: 18px;
    letter-spacing: 0.01em;
    text-transform: uppercase;

    color: $color-night-rider;
  }

  &__list {
    position: relative;

    height: 100%;
    max-height: 976px;
    margin: 0;
    padding: 0;
    overflow: hidden;

    list-style: none;

    &::before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: calc(100% + 21px * 2);
      height: 40px;
      margin: 0 -21px;
      background: linear-gradient(180deg, rgba($color-default-white, 0.8) 0%, rgba($color-default-white, 0) 100%);
    }

    &::after {
      content: "";
      position: absolute;
      bottom: 0;
      left: 0;
      width: calc(100% + 21px * 2);
      height: 40px;
      margin: 0 -21px;
      background: linear-gradient(0deg, rgba($color-default-white, 0.8) 0%, rgba($color-default-white, 0) 100%);
    }
  }

  &__item {
    margin-bottom: 20px;

    &:last-child {
      margin-bottom: 0;
    }
  }

  &__button {
    margin-top: 30px;
  }
}
